import axios from 'axios';
import { injectable } from 'inversify';
import { OrganizationTreeListEntity } from '../domain/entities/organizationEntities';
import TreeRepository from '../domain/repositories/organizationRepository';

@injectable()
export default class TreeRepositoryImpl implements TreeRepository {
  // 获取全部的树形图列表数据
  async requestAllStoreTreeList(): Promise<OrganizationTreeListEntity> {
    const { data } = await axios.get(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/unit/query-org-tree`);
    return data;
  }
}
