import axios from 'axios';
import { injectable } from 'inversify';
import { CommonPagesGeneric } from '../common/config/commonConfig';
import NoticeRepository from '../domain/repositories/noticeRepository';
import {
  NoticeListItemConfig,
  NoticeItemDetailsEntity,
  NoticeDetailsListEntity,
  NoticePlayListItemConfig,
  NoticeApproveListItemConfig,
  StoreWithCountEntity,
  OperateNoticeEntity,
} from '../domain/entities/noticeEntities';
import {
  StoreListItemConfig,
  AdvertisementDeviceListEntity,
} from '../domain/entities/deviceEntities';

@injectable()
export default class NoticeRepositoryImpl implements NoticeRepository {
  // 获取上屏发布-通知列表
  async requestPublishNoticeList(
    page: number,
    size: number,
    status?: string,
    content?: string,
    deviceName?: string,
    storeId?: string,
  ): Promise<CommonPagesGeneric<NoticeListItemConfig>> {
    const { data } = await axios.get(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/notice`, {
      params: {
        page,
        size,
        status,
        content,
        deviceName,
        storeId,
      },
    });
    return data;
  }

  // 获取上屏发布-通知列表-通知详情数据
  async requestNoticeItemDetails(noticeId: number): Promise<NoticeItemDetailsEntity> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/notice/detail`,
      {
        params: {
          noticeId,
        },
      },
    );
    return data;
  }

  // 获取上屏发布-通知列表-通知详情列表数据
  async requestNoticeDetailsListData(
    noticeId: number,
    type: string,
    page: number,
    size: number,
  ): Promise<NoticeDetailsListEntity> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/notice/type/device`,
      {
        params: {
          noticeId,
          type,
          page,
          size,
        },
      },
    );
    return data;
  }

  // 删除上屏发布-通知列表单条数据
  async requestDel(noticeId?: number): Promise<void> {
    await axios.delete(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/notice`, {
      params: { noticeId },
    });
  }

  // 获取播放-通知列表
  async requestPlayNoticeList(
    page: number,
    size: number,
    status?: string,
    content?: string,
    deviceName?: string,
    storeId?: string,
  ): Promise<CommonPagesGeneric<NoticePlayListItemConfig>> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/playlist/notice`,
      {
        params: {
          page,
          size,
          status,
          content,
          deviceName,
          storeId,
        },
      },
    );
    return data;
  }
  // 获取项目/门店列表数据
  async requestStoresList(): Promise<StoreListItemConfig[]> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/store/list`,
    );
    return data;
  }

  // 获取审批管理-通知列表
  async requestApproveNoticeList(
    size: number,
    page: number,
    status?: string,
    content?: string,
    flag?: boolean,
  ): Promise<CommonPagesGeneric<NoticeApproveListItemConfig>> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/approve/notice/page`,
      {
        params: {
          page,
          size,
          status,
          content,
          flag,
        },
      },
    );
    return data;
  }

  // 操作通知/开始/暂停
  async requestOperateNotice(params: OperateNoticeEntity): Promise<void> {
    await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/playlist/notice/operation`,
      params,
    );
  }

  // 获取组织列表数据
  async requestStoreWithCount(deviceType: string): Promise<StoreWithCountEntity[]> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/search-by/store-with-device-count?deviceTypeEnum=${deviceType}`,
    );
    return data;
  }

  // 根据设备类型查询租户下所有设备列表
  async requestAllDeviceByDevicetype(deviceType: string): Promise<AdvertisementDeviceListEntity[]> {
    const { data } = await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/select-all-by-type/${deviceType}`,
    );
    return data;
  }

  // 发布紧急通知
  async requestReleaseNotice(result: NoticeItemDetailsEntity): Promise<void> {
    await axios.post(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/notice`, result);
  }
}
