import axios from 'axios';
import { injectable } from 'inversify';
import ApproveRepository from '../domain/repositories/approveRepository';
import {
  ApproveProgressEntity,
  ApproveSettingListItemConfig,
  ApproveParamsEntity,
  ApproveRulesRequestConfig,
  UnitInfoConfig,
  AdminListDataItem,
} from '../domain/entities/approveEnities';
import { CommonPagesGeneric } from '../common/config/commonConfig';

@injectable()
export default class ApproveRepositoryImpl implements ApproveRepository {
  // 获取上屏发布-通知列表-通知详情-审批进度
  async requestApproveProgress(
    businessType?: string,
    businessId?: number,
  ): Promise<ApproveProgressEntity[]> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/approve/progress`,
      {
        params: { businessType, businessId },
      },
    );
    return data;
  }

  // 获取审批设置列表
  async requestApproveSetting(
    page: number,
    size: number,
    approveType?: string,
    storeId?: string,
    unitId?: string,
  ): Promise<CommonPagesGeneric<ApproveSettingListItemConfig>> {
    const { data } = await axios.get(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/wf-process`, {
      params: {
        page,
        size,
        approveType,
        storeId,
        unitId,
      },
    });
    return data;
  }

  // 判断该组织是否依然存在
  async requestIsExist(id?: number): Promise<number> {
    const { status } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/hpfm/v1/extension/valid-unit/${id}`,
    );
    return status;
  }
  // 删除审批设置列表单条数据
  async requestDel(id?: number): Promise<void> {
    await axios.delete(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/wf-process/delete/${id}`);
  }
  // 审批
  async requestApprove(params: ApproveParamsEntity): Promise<void> {
    await axios.post(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/approve`, params);
  }

  // 创建审批规则
  async requestCreate(result: ApproveRulesRequestConfig): Promise<void> {
    await axios.post(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/wf-process/create`, result);
  }

  // 更新审批规则
  async requestUpdate(result: ApproveRulesRequestConfig): Promise<void> {
    await axios.post(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/wf-process/update`, result);
  }

  // 获取未创建审批设置的组织
  async reauestUnitInfo(): Promise<UnitInfoConfig[]> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/wf-process/units`,
    );
    return data;
  }

  // 获取未创建审批设置的组织
  async reauestAdminInfo(): Promise<AdminListDataItem[]> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/iam/v1/8/approve/users`,
    );
    return data;
  }
}
