import React from 'react';
import { observer } from 'mobx-react';
import {
  Modal,
  Steps,
  Button,
  Form,
  Row,
  Col,
  Input,
  FormInstance,
  Radio,
  Slider,
  InputNumber,
  Select,
  Tabs,
  Table,
  Cascader,
  Tooltip,
} from 'antd';
import { ColumnProps } from 'antd/lib/table';
import { SketchPicker, ColorResult } from 'react-color';
import style from './style.less';
import DI from '../../../../inversify.config';
import { ModalStatus } from '../../../../common/config/commonConfig';
import { AdvertisementDeviceListEntity } from '../../../../domain/entities/deviceEntities';
import CreateNoticeModalViewModel from './viewModel';
import NoticeListViewModel from '../noticeList/viewModel';
import { NOTICE_IDENTIFIER } from '../../../../constants/identifiers';
import { NoticeItemDetailsEntity } from '../../../../domain/entities/noticeEntities';
import NoticePreview from '../../../../common/components/noticePreview';
import closeIcon from '../../../../assets/images/close_icon_normal.svg';
import resultsIcon from '../../../../assets/images/results_icon.svg';
import deleteTable from '../../../../assets/images/delete_icon.svg';

const { Step } = Steps;

interface CreateNoticeModalProps {}
interface CreateNoticeModalState {
  current: number;
  //列表表格列头
  advertingListColumns: ColumnProps<AdvertisementDeviceListEntity>[];
  cashierListColumns: ColumnProps<AdvertisementDeviceListEntity>[];
}

@observer
export default class CreateNoticeModal extends React.Component<
  CreateNoticeModalProps,
  CreateNoticeModalState
> {
  private viewModel = DI.DIContainer.get<CreateNoticeModalViewModel>(
    NOTICE_IDENTIFIER.CREATE_NOTICE_MODAL_VIEW_MODEL,
  );
  private noticeListViewModel = DI.DIContainer.get<NoticeListViewModel>(
    NOTICE_IDENTIFIER.NOTICE_LIST_VIEW_MODEL,
  );
  private cascaderRef = React.createRef<any>();
  private formRef = React.createRef<FormInstance>();
  private deviceFormRef = React.createRef<FormInstance>();

  constructor(props: CreateNoticeModalProps) {
    super(props);
    this.state = {
      current: 0,
      advertingListColumns: [],
      cashierListColumns: [],
    };
  }

  componentDidMount(): void {
    // const { createNoticeModalItemData, onFinishByCreateNotice } = this.noticeListViewModel;
    const { getAdvertisingList, deleteTableItemData } = this.viewModel;
    // const { noticePreviewProps, getAdvertisingList } = this.viewModel;
    getAdvertisingList();
    // const { current } = this.state;
    // 如果改必填项为空 表示新建 否则为编辑
    // 若为新建 则将默认值赋给 单项数据
    // if (!createNoticeModalItemData.content) {
    //   onFinishByCreateNotice(current, noticePreviewProps);
    // }
    this.setState({
      advertingListColumns: [
        {
          title: '项目/门店名称',
          dataIndex: 'storeName',
          key: 'storeName',
          align: 'left',
        },
        {
          title: '所在楼层',
          dataIndex: 'floor',
          key: 'floor',
          align: 'left',
        },
        {
          title: '设备名称',
          dataIndex: 'deviceName',
          key: 'deviceName',
          align: 'left',
        },
        {
          title: '组名名称',
          dataIndex: 'groupName',
          key: 'groupName',
          align: 'left',
        },
        {
          title: '操作',
          key: 'action',
          align: 'left',
          fixed: 'right',
          width: '8%',
          render: (record): JSX.Element => (
            <div>
              <Tooltip title="删除">
                <Button
                  type="text"
                  className={style.opertionBtn}
                  onClick={(): void => deleteTableItemData(record)}
                >
                  <img src={deleteTable} alt="" />
                </Button>
              </Tooltip>
            </div>
          ),
        },
      ],
      cashierListColumns: [
        {
          title: '项目/门店名称',
          dataIndex: 'storeName',
          key: 'storeName',
          align: 'left',
        },
        {
          title: '品牌业态',
          dataIndex: 'brandFormat',
          key: 'brandFormat',
          align: 'left',
        },
        {
          title: '点位品牌名称',
          dataIndex: 'pointBrandName',
          key: 'pointBrandName',
          align: 'left',
        },
        {
          title: '组名名称',
          dataIndex: 'groupName',
          key: 'groupName',
          align: 'left',
        },
        {
          title: '操作',
          key: 'action',
          align: 'left',
          width: '8%',
          fixed: 'right',
          render: (record): JSX.Element => (
            <div>
              <Tooltip title="删除">
                <Button
                  type="text"
                  className={style.opertionBtn}
                  onClick={(): void => deleteTableItemData(record)}
                >
                  <img src={deleteTable} alt="" />
                </Button>
              </Tooltip>
            </div>
          ),
        },
      ],
    });
  }

  private next = (): void => {
    const { current } = this.state;
    this.setState({
      current: current + 1,
    });
  };

  private prev = (): void => {
    const { current } = this.state;
    this.setState({
      current: current - 1,
    });
  };

  //  设置预览prop参数
  private onChange = (key: string): void => {
    const { setNoticePreviewProps } = this.viewModel;
    setNoticePreviewProps(key, this.formRef.current?.getFieldValue(key));
  };

  // 设备content
  private tabsContent(): JSX.Element {
    const {
      setDevice,
      setWayType,
      pageChange,
      specificDevice,
      deviceScope,
      deviceTypes,
      cascaderOptions,
      advertingListData,
      advertingListDataSource,
      cashierListDataSource,
      ledListDataSource,
      getAdvertingListParams,
      storeNameList,
    } = this.viewModel;
    const { advertingListColumns, cashierListColumns } = this.state;

    return (
      <div>
        {deviceScope === 'part' && (
          <Col span={24}>
            <Row>
              <Col span={11} className={style.screening}>
                <Form.Item label="选择方式" name="way">
                  <Select
                    placeholder="请选择选择方式"
                    style={{ width: '200px' }}
                    onChange={setWayType}
                    defaultValue="project"
                  >
                    <Select.Option value="project">按项目/门店选择</Select.Option>
                    <Select.Option value="group">按分组选择</Select.Option>
                  </Select>
                </Form.Item>
              </Col>
              <Col span={2} />
              <Col span={11} className={style.screening}>
                <Form.Item label="具体设备" name="deviceScope" className={style.scope}>
                  <Cascader
                    ref={this.cascaderRef}
                    style={{ width: '200px' }}
                    multiple
                    showSearch
                    defaultValue={specificDevice}
                    onChange={setDevice}
                    options={cascaderOptions}
                    maxTagCount="responsive"
                    placeholder="请选择具体设备"
                  />
                </Form.Item>
              </Col>
            </Row>
          </Col>
        )}
        <div className={style.result}>
          <img src={resultsIcon} alt="" />
          <span>
            选择结果：{storeNameList.length}个项目/门店，
            {advertingListDataSource.length +
              cashierListDataSource.length +
              ledListDataSource.length}
            台设备
          </span>
        </div>
        <div className={style.tableContent}>
          <Table
            pagination={{
              size: 'small',
              showSizeChanger: false,
              showQuickJumper: false,
              total: advertingListData.totalElements || 0,
              pageSize: getAdvertingListParams.size,
              current: getAdvertingListParams.page + 1,
              onChange: pageChange,
            }}
            scroll={{ x: 900 }}
            className={style.table}
            rowClassName={style.tableRow}
            columns={deviceTypes === 'CASHIER' ? cashierListColumns : advertingListColumns}
            dataSource={this.switchDataSource(deviceTypes)}
          />
        </div>
      </div>
    );
  }

  public switchDataSource = (deviceType: string): AdvertisementDeviceListEntity[] => {
    const { advertingListDataSource, cashierListDataSource, ledListDataSource } = this.viewModel;
    switch (deviceType) {
      case 'ADMACHINE':
        return advertingListDataSource;
      case 'CASHIER':
        return cashierListDataSource;
      case 'LED':
        return ledListDataSource;
      default:
        return advertingListDataSource;
    }
  };
  public switchDeviceTypes = (value: string): void => {
    const { switchDeviceTypes } = this.viewModel;
    switchDeviceTypes(value);
    console.log(this.cascaderRef);
  };

  public render(): JSX.Element {
    const {
      createNoticeModalVisible,
      setCreateNoticeModalVisible,
      noticePreviewProps,
      bgColorPickerVisible,
      fontColorPickerVisible,
      setColorPickerVisible,
      setNoticePreviewProps,
      deviceScopeChange,
      modalType,
      onRelease,
    } = this.viewModel;
    const {
      createNoticeModalItemData,
      onFinishByCreateNotice,
      setCreateNoticeModalItemData,
    } = this.noticeListViewModel;
    const { current } = this.state;

    return (
      <Modal
        visible={createNoticeModalVisible}
        width={950}
        closable={false}
        footer={null}
        wrapClassName={style.createNoticeModalContainer}
        destroyOnClose
      >
        <div className={style.createNoticeModalContent}>
          <div className={style.modalHeader}>
            {modalType === ModalStatus.Creat ? '发布紧急通知' : '编辑紧急通知'}
            <Button type="text" onClick={(): void => setCreateNoticeModalVisible(false)}>
              <img src={closeIcon} alt="" />
            </Button>
          </div>
          <div>
            <Steps current={current}>
              <Step key="设置内容" title="设置内容" />
              <Step key="选择设备" title="选择设备" />
            </Steps>
            {current === 0 && (
              <div className="steps-content">
                <div className={style.stepContentWrapper}>
                  <Form
                    labelCol={{ span: 6 }}
                    wrapperCol={{ span: 14 }}
                    onFinish={(values: NoticeItemDetailsEntity): void => {
                      this.next();
                      onFinishByCreateNotice(current, values);
                    }}
                    initialValues={{ remember: true }}
                    ref={this.formRef}
                  >
                    <Row>
                      <Col
                        span={4}
                        style={{ display: 'flex', justifyItems: 'center', alignItems: 'center' }}
                      >
                        <span
                          style={{
                            fontFamily: 'SimSun, sans-serif',
                            color: '#f5222d',
                            fontSize: '14px',
                            marginRight: '4px',
                          }}
                        >
                          *
                        </span>
                        <span>颜色选择</span>
                      </Col>
                      <Col span={6} className={style.formWrapper}>
                        <div
                          className={style.swatch}
                          onClick={(): void => setColorPickerVisible('fontColor', true)}
                        >
                          <div
                            className={style.color}
                            style={{ backgroundColor: noticePreviewProps.fontColor }}
                          />
                          字体颜色
                        </div>
                        {fontColorPickerVisible && (
                          <div className={style.popover}>
                            <div
                              className={style.cover}
                              onClick={(): void => setColorPickerVisible('fontColor', false)}
                            />
                            <SketchPicker
                              color={noticePreviewProps.fontColor}
                              onChange={(value: ColorResult): void => {
                                setNoticePreviewProps('fontColor', value.hex);
                                setCreateNoticeModalItemData(value.hex, 'color');
                              }}
                            />
                          </div>
                        )}
                      </Col>
                      <Col span={6} className={style.formWrapper}>
                        <div
                          className={style.swatch}
                          onClick={(): void => setColorPickerVisible('bgColor', true)}
                        >
                          <div
                            className={style.color}
                            style={{ backgroundColor: noticePreviewProps.bgColor }}
                          />
                          背景颜色
                        </div>
                        {bgColorPickerVisible && (
                          <div className={style.popover}>
                            <div
                              className={style.cover}
                              onClick={(): void => setColorPickerVisible('bgColor', false)}
                            />
                            <SketchPicker
                              color={noticePreviewProps.bgColor}
                              onChange={(value: ColorResult): void => {
                                setNoticePreviewProps('bgColor', value.hex);
                                setCreateNoticeModalItemData(value.hex, 'backgroundColor');
                              }}
                            />
                          </div>
                        )}
                      </Col>
                    </Row>
                    <Row>
                      <Col span={18}>
                        <Form.Item
                          labelCol={{ span: 5 }}
                          wrapperCol={{ span: 19 }}
                          label="不透明度"
                          name="backgroundTransparency"
                          initialValue={createNoticeModalItemData.backgroundTransparency}
                          rules={[{ required: true }]}
                        >
                          <Slider
                            min={0}
                            max={100}
                            step={1}
                            onChange={(): void => this.onChange('opacity')}
                          />
                        </Form.Item>
                      </Col>
                      <Col span={6}>
                        <Form.Item
                          wrapperCol={{ span: 24 }}
                          name="backgroundTransparency"
                          initialValue={createNoticeModalItemData.backgroundTransparency}
                          rules={[{ required: true }]}
                        >
                          <InputNumber
                            min={0}
                            max={100}
                            style={{ margin: '0 16px' }}
                            step={1}
                            addonAfter={<span>%</span>}
                            onChange={(): void => this.onChange('opacity')}
                          />
                        </Form.Item>
                      </Col>
                    </Row>
                    <Row>
                      <Col span={24} className={style.formWrapper}>
                        <Form.Item
                          labelCol={{ span: 4 }}
                          wrapperCol={{ span: 20 }}
                          label="内容描述"
                          name="content"
                          initialValue={createNoticeModalItemData.content}
                          rules={[{ required: true }]}
                        >
                          <Input.TextArea
                            onBlur={(): void => this.onChange('content')}
                            showCount
                            maxLength={100}
                            rows={4}
                            placeholder="请输入内容"
                          />
                        </Form.Item>
                      </Col>
                    </Row>
                    <Row>
                      <Col span={24} className={style.formWrapper}>
                        <Form.Item
                          labelCol={{ span: 4 }}
                          wrapperCol={{ span: 20 }}
                          label="文字大小"
                          name="sizeCode"
                          initialValue={createNoticeModalItemData.sizeCode}
                          rules={[{ required: true }]}
                        >
                          <Select placeholder="文字大小">
                            <Select.Option value="5">5%</Select.Option>
                            <Select.Option value="10">10%</Select.Option>
                            <Select.Option value="15">15%</Select.Option>
                            <Select.Option value="20">20%</Select.Option>
                            <Select.Option value="25">25%</Select.Option>
                            <Select.Option value="30">30%</Select.Option>
                            <Select.Option value="40">40%</Select.Option>
                            <Select.Option value="50">50%</Select.Option>
                            <Select.Option value="60">60%</Select.Option>
                            <Select.Option value="70">70%</Select.Option>
                            <Select.Option value="80">80%</Select.Option>
                            <Select.Option value="90">90%</Select.Option>
                            <Select.Option value="100">100%</Select.Option>
                          </Select>
                        </Form.Item>
                      </Col>
                    </Row>
                    <Row>
                      <Col span={24} className={style.formWrapper}>
                        <Form.Item
                          labelCol={{ span: 4 }}
                          wrapperCol={{ span: 20 }}
                          label="文字位置"
                          name="locationCode"
                          initialValue={createNoticeModalItemData.locationCode}
                          rules={[{ required: true }]}
                        >
                          <Select placeholder="文字位置">
                            <Select.Option value="顶部">顶部</Select.Option>
                            <Select.Option value="中部">中部</Select.Option>
                            <Select.Option value="底部">底部</Select.Option>
                          </Select>
                        </Form.Item>
                      </Col>
                    </Row>
                    <Row>
                      <Col span={24} className={style.formWrapper}>
                        <Form.Item
                          labelCol={{ span: 4 }}
                          wrapperCol={{ span: 20 }}
                          label="滚动速度"
                          name="speedCode"
                          initialValue={createNoticeModalItemData.speedCode}
                          rules={[{ required: true }]}
                        >
                          <Radio.Group onChange={(): void => this.onChange('speed')}>
                            <Radio value="较慢">较慢</Radio>
                            <Radio value="正常">正常</Radio>
                            <Radio value="较快">较快</Radio>
                          </Radio.Group>
                        </Form.Item>
                      </Col>
                    </Row>
                    <Row>
                      <Col span={10} className={style.formWrapper}>
                        <Form.Item
                          labelCol={{ span: 10 }}
                          wrapperCol={{ span: 11 }}
                          label="展示时长"
                          initialValue={createNoticeModalItemData.hour}
                          tooltip="上限：1000小时"
                          name="hour"
                          rules={[{ required: true }]}
                        >
                          <InputNumber min={0} max={1000} step={1} addonAfter={<span>时</span>} />
                        </Form.Item>
                      </Col>
                      <Col span={6} className={style.formWrapper}>
                        <Form.Item
                          wrapperCol={{ span: 20 }}
                          initialValue={createNoticeModalItemData.minute}
                          name="minute"
                          rules={[{ required: true }]}
                        >
                          <InputNumber min={0} max={59} step={1} addonAfter={<span>分</span>} />
                        </Form.Item>
                      </Col>
                      <Col span={6} className={style.formWrapper}>
                        <Form.Item
                          wrapperCol={{ span: 20 }}
                          initialValue={createNoticeModalItemData.second}
                          name="second"
                          rules={[{ required: true }]}
                        >
                          <InputNumber min={0} max={59} step={1} addonAfter={<span>秒</span>} />
                        </Form.Item>
                      </Col>
                    </Row>
                    <Row>
                      <Col span={4} style={{ marginLeft: '10px' }}>
                        效果预览
                      </Col>
                      <Col span={19}>
                        <NoticePreview noticePreviewProps={noticePreviewProps} />
                      </Col>
                    </Row>
                    <div className="steps-action">
                      <Button
                        type="primary"
                        ghost
                        style={{ margin: '0 8px' }}
                        onClick={(): void => setCreateNoticeModalVisible(false)}
                      >
                        取消
                      </Button>
                      <Button type="primary" htmlType="submit">
                        下一步
                      </Button>
                    </div>
                  </Form>
                </div>
              </div>
            )}
            {current === 1 && (
              <div>
                <Form>
                  <div className={style.device}>
                    <Form.Item label="发送范围" name="advert" className={style.scope}>
                      <Radio.Group onChange={deviceScopeChange} defaultValue="part">
                        <Radio value="part">部分设备</Radio>
                        <Radio value="all">全部设备</Radio>
                      </Radio.Group>
                    </Form.Item>
                  </div>
                  <div className={style.tabsContainer}>
                    <Tabs defaultActiveKey="1" onChange={(e: string): void => this.switchDeviceTypes(e)}>
                      <Tabs.TabPane tab="广告机" key="ADMACHINE">
                        {this.tabsContent()}
                      </Tabs.TabPane>
                      <Tabs.TabPane tab="收银机" key="CASHIER">
                        {this.tabsContent()}
                      </Tabs.TabPane>
                      <Tabs.TabPane tab="LED" key="LED">
                        {this.tabsContent()}
                      </Tabs.TabPane>
                    </Tabs>
                  </div>
                </Form>
                <div className="steps-action">
                  <Button
                    type="primary"
                    ghost
                    style={{ margin: '0 8px' }}
                    onClick={(): void => setCreateNoticeModalVisible(false)}
                  >
                    取消
                  </Button>
                  <Button
                    type="primary"
                    style={{ margin: '0 8px' }}
                    onClick={(): void => this.prev()}
                  >
                    上一步
                  </Button>
                  <Button type="primary" onClick={(): void => onRelease(this.noticeListViewModel)}>
                    发布
                  </Button>
                </div>
              </div>
            )}
          </div>
        </div>
      </Modal>
    );
  }
}
