import React from 'react';
import { observer } from 'mobx-react';
import { Modal, Button, Input, Form, Row, Col, Select, Radio, FormInstance } from 'antd';
import style from './style.less';

import DI from '../../../inversify.config';
import AdvertisementMachineViewModel from '../advertisementMachine/viewModel';
import RaspberryMachinePropsViewModel from '../raspberryMachine/viewModel';
import DeviceListViewModel from '../deviceList/viewModel';
import { DEVICE_IDENTIFIER } from '../../../constants/identifiers';
import DeviceEditModalViewModel from './viewModel';
import { DeviceType, ModalStatus } from '../../../common/config/commonConfig';
import closeIcon from '../../../assets/images/close_icon_normal.svg';
import { DeviceRecordListItemConfig } from '../../../domain/entities/deviceEntities';

@observer
export default class DeviceEditModal extends React.Component {
  private formRef = React.createRef<FormInstance>();
  private viewModel = DI.DIContainer.get<DeviceEditModalViewModel>(
    DEVICE_IDENTIFIER.DEVICE_EDIT_MODEL_VIEW_MODEL,
  );

  private advertisementMachineViewModel = DI.DIContainer.get<AdvertisementMachineViewModel>(
    DEVICE_IDENTIFIER.ADVERTISEMENT_MACHINE_VIEW_MODEL,
  );

  private raspberryMachinePropsViewModel = DI.DIContainer.get<RaspberryMachinePropsViewModel>(
    DEVICE_IDENTIFIER.RASPBERRY_MACHINE_VIEW_MODEL,
  );

  private deviceListViewModel = DI.DIContainer.get<DeviceListViewModel>(
    DEVICE_IDENTIFIER.DEVICE_LIST_VIEW_MODEL,
  );

  public render(): JSX.Element {
    const {
      deviceEditVisible,
      onFinish,
      selectedDeviceType,
      selectedDeviceDate,
      deviceEditModalSwitch,
      modalType,
      supportedFeatureData,
    } = this.viewModel;

    const { deviceTypeData } = this.deviceListViewModel;

    const supportedFeature = supportedFeatureData.map((item, index) => {
      return (
        <Radio key={`supported_feature${index}`} value={item.value === '1' ? 1 : 0}>
          {item.meaning}
        </Radio>
      );
    });

    return (
      <Modal
        visible={deviceEditVisible}
        width={729}
        closable={false}
        footer={null}
        wrapClassName={style.deviceEditContainer}
        destroyOnClose
      >
        <div className={style.deviceEditContent}>
          <div className={style.modalHeader}>
            {this.generateTitle()}
            <Button type="text" onClick={(): void => deviceEditModalSwitch()}>
              <img src={closeIcon} alt="" />
            </Button>
          </div>
          <div className={style.formContainer}>
            {selectedDeviceType !== DeviceType.Raspberry ? (
              <Form
                onFinish={(values: DeviceRecordListItemConfig): void => {
                  onFinish(values, this.advertisementMachineViewModel);
                }}
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 16 }}
                ref={this.formRef}
              >
                <Row>
                  <Col span={12}>
                    <Form.Item
                      label="组织名称"
                      name="organization"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.organization}
                    >
                      <Input />
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="设备类型"
                      name="type"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.type}
                    >
                      <Select
                        getPopupContainer={(triggerNode): HTMLElement => triggerNode.parentElement}
                      >
                        {deviceTypeData.map((item, index) => {
                          return (
                            <Select.Option value={item.value} key={`device_type_${index}`}>
                              {item.meaning}
                            </Select.Option>
                          );
                        })}
                      </Select>
                    </Form.Item>
                  </Col>
                  {(selectedDeviceType === DeviceType.Advertisement ||
                    selectedDeviceType === DeviceType.Led) && (
                    <Col span={12}>
                      <Form.Item
                        label="设备名称"
                        name="name"
                        rules={[{ required: true }]}
                        initialValue={selectedDeviceDate.name}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                  )}
                  {selectedDeviceType === DeviceType.Cashier && (
                    <Col span={12}>
                      <Form.Item
                        label="点位品牌名称"
                        name="pointBrandName"
                        rules={[{ required: true }]}
                        initialValue={selectedDeviceDate.pointBrandName}
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                  )}
                  {selectedDeviceType === DeviceType.Cashier && (
                    <Col span={12}>
                      <Form.Item
                        label="品牌业态"
                        name="brandBusiness"
                        rules={[{ required: true }]}
                        initialValue="美妆护肤"
                      >
                        <Input />
                      </Form.Item>
                    </Col>
                  )}
                  <Col span={12}>
                    <Form.Item
                      label="所属项目/门店"
                      name="storeName"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.storeName}
                    >
                      <Select
                        getPopupContainer={(triggerNode): HTMLElement => triggerNode.parentElement}
                      >
                        <Select.Option value="广告机">广告机</Select.Option>
                        <Select.Option value="LED">LED</Select.Option>
                        <Select.Option value="收银机">收银机</Select.Option>
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="所在楼层"
                      name="floor"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.floor}
                    >
                      <Select
                        getPopupContainer={(triggerNode): HTMLElement => triggerNode.parentElement}
                      >
                        <Select.Option value="L1">L1</Select.Option>
                        <Select.Option value="L2">L2</Select.Option>
                        <Select.Option value="L3">L3</Select.Option>
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="分辨率值"
                      name="resolution"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.resolution}
                    >
                      <Select
                        getPopupContainer={(triggerNode): HTMLElement => triggerNode.parentElement}
                      >
                        <Select.Option value="1920">1920*1080</Select.Option>
                        <Select.Option value="1680">1680*1050</Select.Option>
                        <Select.Option value="1600">1600*900</Select.Option>
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="IP地址"
                      name="ipAddress"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.ipAddress}
                    >
                      <Input />
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="设备状态"
                      name="status"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.status === '0' ? 0 : 1}
                    >
                      <Radio.Group>
                        <Radio value={1}>启用</Radio>
                        <Radio value={0}>禁用</Radio>
                      </Radio.Group>
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="设备分组"
                      name="groupNameList"
                      initialValue={selectedDeviceDate.groupNameList}
                    >
                      <Input />
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="分管部门"
                      name="department"
                      initialValue={selectedDeviceDate.department}
                    >
                      <Select
                        getPopupContainer={(triggerNode): HTMLElement => triggerNode.parentElement}
                      >
                        <Select.Option value="工程部">工程部</Select.Option>
                        <Select.Option value="项目部">项目部</Select.Option>
                        <Select.Option value="开发部">开发部</Select.Option>
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="特征广告"
                      name="supportedFeature"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.supportedFeature ? 1 : 0}
                    >
                      <Radio.Group>
                        {supportedFeature}
                      </Radio.Group>
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item label="Mac地址">
                      <span>{selectedDeviceDate.macAddress}</span>
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item label="操作系统">
                      <span>{selectedDeviceDate.os}</span>
                    </Form.Item>
                  </Col>
                </Row>
                <div className={style.bottomButton}>
                  <Button type="primary" htmlType="submit">
                    保存
                  </Button>
                  <Button type="primary" onClick={(): void => deviceEditModalSwitch()}>
                    关闭
                  </Button>
                </div>
              </Form>
            ) : (
              <Form
                onFinish={(values: DeviceRecordListItemConfig): void => {
                  onFinish(values, undefined, this.raspberryMachinePropsViewModel);
                }}
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 16 }}
                ref={this.formRef}
              >
                <Row>
                  <Col span={12}>
                    <Form.Item
                      label="所在组织"
                      name="organization"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.organization}
                    >
                      <Select disabled={modalType === ModalStatus.View}>
                        <Select.Option value="深圳湾">深圳湾</Select.Option>
                        <Select.Option value="深圳湾2">深圳湾2</Select.Option>
                        <Select.Option value="深圳湾3">深圳湾3</Select.Option>
                      </Select>
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="设备名称"
                      name="name"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.name}
                    >
                      <Input disabled={modalType === ModalStatus.View} />
                    </Form.Item>
                  </Col>
                </Row>
                <Row>
                  <Col span={12}>
                    <Form.Item
                      label="Mac地址"
                      name="macAddress"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.macAddress}
                    >
                      <Input disabled={modalType === ModalStatus.View} />
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="IP地址"
                      name="ipAddress"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.ipAddress}
                    >
                      <Input disabled={modalType === ModalStatus.View} />
                    </Form.Item>
                  </Col>
                </Row>
                <Row>
                  <Col span={12}>
                    <Form.Item
                      label="子网掩码"
                      name="subMask"
                      rules={[{ required: true }]}
                      initialValue={selectedDeviceDate.subMask}
                    >
                      <Input disabled={modalType === ModalStatus.View} />
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="设备状态"
                      name="status"
                      rules={[{ required: true }]}
                      initialValue={
                        // eslint-disable-next-line no-nested-ternary
                        selectedDeviceDate.status === '0'
                          ? 0
                          : selectedDeviceDate.status === '1'
                          ? 1
                          : undefined
                      }
                    >
                      <Radio.Group disabled={modalType === ModalStatus.View}>
                        <Radio value={1}>启用</Radio>
                        <Radio value={0}>禁用</Radio>
                      </Radio.Group>
                    </Form.Item>
                  </Col>
                </Row>
                <Row>
                  <Col span={12}>
                    <Form.Item
                      label="设备品牌"
                      name="deviceBrand"
                      initialValue={selectedDeviceDate.deviceBrand}
                    >
                      <Input disabled={modalType === ModalStatus.View} />
                    </Form.Item>
                  </Col>
                  <Col span={12}>
                    <Form.Item
                      label="分管部门"
                      name="department"
                      initialValue={selectedDeviceDate.department}
                    >
                      <Select disabled={modalType === ModalStatus.View}>
                        <Select.Option value="工程部">工程部</Select.Option>
                        <Select.Option value="项目部">项目部</Select.Option>
                        <Select.Option value="开发部">开发部</Select.Option>
                      </Select>
                    </Form.Item>
                  </Col>
                </Row>
                {modalType === ModalStatus.View ? (
                  <div className={style.bottomButton}>
                    <Button type="primary" onClick={(): void => deviceEditModalSwitch()}>
                      关闭
                    </Button>
                  </div>
                ) : (
                  <div className={style.bottomButton}>
                    <Button onClick={(): void => deviceEditModalSwitch()}>关闭</Button>
                    <Button type="primary" htmlType="submit">
                      保存
                    </Button>
                    <Button type="primary" onClick={(): void => deviceEditModalSwitch()}>
                      保存并继续添加
                    </Button>
                  </div>
                )}
              </Form>
            )}
          </div>
        </div>
      </Modal>
    );
  }

  private generateTitle = (): string => {
    const { selectedDeviceType, modalType } = this.viewModel;
    if (selectedDeviceType === DeviceType.Raspberry) {
      if (modalType === ModalStatus.Edit) {
        return '编辑树莓派';
      }
      if (modalType === ModalStatus.Creat) {
        return '新增树莓派';
      }
      if (modalType === ModalStatus.View) {
        return '查看设备';
      }
    }
    return '编辑设备';
  };
}
