import axios from 'axios';
import { injectable } from 'inversify';
import FileRepository from '../domain/repositories/fileRepository';
import { TemplateDownloadEntity } from '../domain/entities/fileEntities';

@injectable()
export default class FileRepositoryImpl implements FileRepository {
  // 上传文件 返回文件url
  async uploadForUrl(
    file: File,
    bucketName: string,
    fileName?: string,
    progressCallback?: (percentage: number) => void,
  ): Promise<string> {
    const formData = new FormData();
    formData.append('file', file);
    const { data } = await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/hfle/v1/files/multipart`,
      formData,
      {
        params: {
          bucketName,
          fileName: fileName || file.name,
        },
        onUploadProgress: (e) => {
          progressCallback && progressCallback(Math.round((e.loaded * 100) / e.total));
        },
      },
    );
    return data;
  }

  // 上传文件 返回文件key
  async uploadForKey(
    file: File,
    bucketName: string,
    fileName?: string,
    progressCallback?: (percentage: number) => void,
  ): Promise<string> {
    const formData = new FormData();
    formData.append('file', file);
    const { data } = await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/hfle/v1/files/secret-multipart`,
      formData,
      {
        params: {
          bucketName,
          fileName: fileName || file.name,
        },
        onUploadProgress: (e) => {
          progressCallback && progressCallback(Math.round((e.loaded * 100) / e.total));
        },
      },
    );
    return data;
  }

  // 根据文件key 获取文件url(临时链接)
  async getPreviewUrl(fileKey: string): Promise<TemplateDownloadEntity> {
    const { data } = await axios.get(`${window.__FE_CONF_PROV__.apiPublicUrl}/hfle/v1/files/file-url`, {
      params: {
        fileKey,
      },
    });
    return data;
  }
}
