import moment, { Moment } from 'moment';
import { injectable, inject } from 'inversify';
import { action, makeObservable, observable, runInAction } from 'mobx';
import { DataNode } from 'rc-cascader/lib/interface';
import ExportModalViewModel, { OptionsType, FormData } from './viewModel';
import { ADVERTISEMENT_IDENTIFIER } from '../../../../constants/identifiers';
import AdvertisementUseCase from '../../../../domain/useCases/advertisementUseCase';
import { Devices } from '../../../../domain/entities/advertisementEntities';
import utils from '../../../../utils/index';

@injectable()
export default class ExportModalViewModelImpl implements ExportModalViewModel {
  // advertisementUseCase
  @inject(ADVERTISEMENT_IDENTIFIER.ADVERTISEMENT_USE_CASE)
  private AdvertisementUseCase!: AdvertisementUseCase;
  //标签弹窗状态
  public exportModalVisible: boolean;
  //标签弹窗状态
  public options: OptionsType[];
  public optionsCashier: OptionsType[];
  public optionsLed: OptionsType[];
  public query: string;
  public optionsAd: OptionsType[];
  public advertismentIds: number[];
  public deviceIds: number[];

  public deviceValue: string[] | React.Key[];
  public advertismentValue: string[] | React.Key[];
  public constructor() {
    this.options = [];
    this.optionsCashier = [];
    this.optionsLed = [];
    this.optionsAd = [];
    this.query = 'advertising';
    this.exportModalVisible = false;
    this.advertismentIds = [];
    this.deviceIds = [];
    this.deviceValue = [];
    this.advertismentValue = [];
    makeObservable(this, {
      advertismentValue: observable,
      deviceValue: observable,
      deviceIds: observable,
      advertismentIds: observable,
      optionsAd: observable,
      query: observable,
      exportModalVisible: observable,
      options: observable,
      optionsCashier: observable,
      optionsLed: observable,
      formOnFinish: action,
      setExportModalVisible: action,
      getSpecificDevices: action,
      queryChange: action,
      setDevice: action,
      setAdvertisement: action,
    });
  }
  //选择方式
  public queryChange = (e: string): void => {
    this.query = e;
    this.advertismentIds = [];
    this.deviceIds = [];
    this.deviceValue = [];
    this.advertismentValue = [];
  };
  // 选择广告
  public setAdvertisement = (value: React.Key[], selectedOptions: DataNode[]): void => {
    this.advertismentValue = value;
    selectedOptions.forEach((item) => { 
      // 如果等于1的话，选择了某个门店下的所有的数据
      if (((item as unknown) as Devices[]).length === 1) {
        this.advertismentIds.push(Number(((item as unknown) as Devices[])[0].value) || 0);
      }
    });
  };

  // 选择设备
  public setDevice = (value: React.Key[], selectedOptions: DataNode[]): void => {
    this.deviceValue = value;
    selectedOptions.forEach((item) => {
      // 如果等于3的话，选择了第3级的数据
      if (((item as unknown) as Devices[]).length === 3) {
        // 字段名转换
        let data = JSON.stringify(((item as unknown) as Devices[])[2]);
        data = data.replaceAll('label', 'deviceName').replaceAll('value', 'id');
        const replaceData = JSON.parse(data);
        // 赋值给列表数据
        this.deviceIds.push(replaceData.id);
      }
      // 如果等于2的话，选择了某个楼层下的所有的数据
      if (((item as unknown) as Devices[]).length === 2) {
        ((item as unknown) as Devices[])[1].children?.forEach((floors) => {
          let data = JSON.stringify(floors);
          data = data.replaceAll('label', 'deviceName').replaceAll('value', 'id');
          const replaceData = JSON.parse(data);
          // 赋值给列表数据
          this.deviceIds.push(replaceData.id);
        });
      }

      // 如果等于1的话，选择了某个门店下的所有的数据
      if (((item as unknown) as Devices[]).length === 1) {
        ((item as unknown) as Devices[])[0].children?.forEach((project) => {
          project.children?.forEach((projectAll) => {
            let data = JSON.stringify(projectAll);
            data = data.replaceAll('label', 'deviceName').replaceAll('value', 'id');
            const replaceData = JSON.parse(data);
            // 赋值给列表数据
            this.deviceIds.push(replaceData.id);
          });
        });
      }
    });
  };

  // 获取具体广告
  public getSpecificAd = async (): Promise<void> => {
    const unitId = 64;
    try {
      const data = await this.AdvertisementUseCase.getSpecificAd(unitId);
      runInAction(() => {
        let resData = JSON.stringify(data);
        resData = resData.replaceAll('adName', 'label').replaceAll('id', 'value');
        this.optionsAd = JSON.parse(resData);
      });
    } catch (e) {
      runInAction(() => {
        this.optionsAd = [];
      });
    }
  };

  // 获取具体设备
  public getSpecificDevices = async (deviceTypeEnum: string): Promise<void> => {
    try {
      const data = await this.AdvertisementUseCase.getSpecificDevices(deviceTypeEnum);
      runInAction(() => {
        let resData = JSON.stringify(data);
        resData = resData
          .replaceAll('floors', 'children')
          .replaceAll('projectName', 'label')
          .replaceAll('projectId', 'value')
          .replaceAll('devices', 'children')
          .replaceAll('floorName', 'label')
          .replaceAll('floorCode', 'value')
          .replaceAll('deviceName', 'label')
          .replaceAll('id', 'value');
        if (deviceTypeEnum === 'ADMACHINE') {
          this.options.push(JSON.parse(resData)[0]);
        }
        if (deviceTypeEnum === 'CASHIER') {
          this.optionsCashier = JSON.parse(resData);
          this.options.push(this.optionsCashier[0]);
        }
        if (deviceTypeEnum === 'LED') {
          this.optionsLed = JSON.parse(resData);
          this.options.push(this.optionsLed[0]);
        }
      });
    } catch (e) {
      runInAction(() => {
        // this.options = [];
      });
    }
  };
  //表单提交成功事件
  public formOnFinish = (values: FormData): void => {

    const dataSourceAd = this.advertismentIds.filter((item, index) => {
      const idx = this.advertismentIds.findIndex((i) => {
        return item === i;
      });
      return index === idx;
    });


    const dataSourceDevice = this.deviceIds.filter((item, index) => {
      const idx = this.deviceIds.findIndex((i) => {
        return item === i;
      });
      return index === idx;
    });
    console.log(JSON.parse(JSON.stringify(this.deviceIds)), JSON.parse(JSON.stringify(dataSourceDevice))) 
    const { time } = values;
    let startDate = ''
    let endDate = ''
    if (time) {
      startDate = time[0].format('YYYY-MM-DD');
      endDate = time[1].format('YYYY-MM-DD');
    }
    if(this.query === 'advertising'){
      this.exportData(startDate, endDate, dataSourceAd)
    }else{
      this.deviceData(startDate, endDate, dataSourceDevice)
    }
   

  };

  // 设备导出
  public deviceData = async (startDate:string, endDate: string, dataSourceDevice: number[]): Promise<void> => {
    const fillerType = 'single-sheet'
    const unitId = 64;
    const deviceIds = dataSourceDevice.join(',')
    const idsValue = '1,2,3,4,5,6,7'
    const exportTypeData = 'DATA'
    await this.AdvertisementUseCase
      .deviceExport(fillerType, exportTypeData, unitId, startDate, endDate, deviceIds, idsValue)
      .then((res) => {
        const eleLink = document.createElement('a');
        eleLink.download = `设备信息详单${moment().format('YYYYMMDD')}.xlsx`;
        eleLink.style.display = 'none';
        eleLink.href = URL.createObjectURL(res);
        document.body.appendChild(eleLink);
        eleLink.click();
        document.body.removeChild(eleLink);
        utils.globalMessge({
          content: '导出成功!',
          type: 'success',
        });
      })
      .catch((err) => {
        utils.globalMessge({
          content: `导出失败${err.message}!`,
          type: 'error',
        });
      });
  };

  // 广告导出
  public exportData = async (startDate:string, endDate: string, dataSourceAd: number[]): Promise<void> => {
    const fillerType = 'single-sheet'
    const unitId = 64;
    const adIds = dataSourceAd.join(',')
    const idsValue = '1,2,3,4,5,6'
    const exportTypeData = 'DATA'
    await this.AdvertisementUseCase
      .advertismentExport(fillerType, exportTypeData, unitId, startDate, endDate, adIds, idsValue)
      .then((res) => {
        const eleLink = document.createElement('a');
        eleLink.download = `广告信息详单${moment().format('YYYYMMDD')}.xlsx`;
        eleLink.style.display = 'none';
        eleLink.href = URL.createObjectURL(res);
        document.body.appendChild(eleLink);
        eleLink.click();
        document.body.removeChild(eleLink);
        utils.globalMessge({
          content: '导出成功!',
          type: 'success',
        });
      })
      .catch((err) => {
        utils.globalMessge({
          content: `导出失败${err.message}!`,
          type: 'error',
        });
      });
  };

  //设置标签model显示隐藏
  public setExportModalVisible = (): void => {
    this.exportModalVisible = !this.exportModalVisible;
  };
}
