import React from 'react';
import { reaction } from 'mobx';
import { observer } from 'mobx-react';
import { Tree, Row, Col } from 'antd';
import style from './style.less';

import DI from '../../../inversify.config';
import { ORGANIZATION_TREE_IDENTIFIER } from '../../../constants/identifiers';
import OrganizationTreeViewModel from './viewModel';
import { OrganizationListEntity } from '../../../domain/entities/organizationEntities';

interface OrganizationTreeProps {
  // 传入组件的classname
  treeWrapper?: string;
  // 获取当前选中的组织信息
  getCurrentSelectedInfo(TreeData: OrganizationListEntity): void;
  // 是否展示连接线
  showLine?: boolean | { showLeafIcon: boolean };
}

@observer
export default class OrganizationTree extends React.Component<OrganizationTreeProps> {
  private viewModel = DI.DIContainer.get<OrganizationTreeViewModel>(
    ORGANIZATION_TREE_IDENTIFIER.ORGANIZATION_TREE_VIEW_MODEL,
  );

  constructor(props: OrganizationTreeProps) {
    super(props);
    const { getCurrentSelectedInfo } = this.props;
    reaction(
      () => this.viewModel.selectedOrgTreeData,
      (data) => {
        getCurrentSelectedInfo(data);
      },
    );
  }

  componentDidMount(): void {
    const { getTreeData } = this.viewModel;
    getTreeData();
  }

  public render(): JSX.Element {
    const {
      TreeData,
      treeSelect,
      expandedKeys,
      onExpand,
      autoExpandParent,
      isStore,
      selectedStoreTreeData,
      selectedOrgTreeData,
    } = this.viewModel;
    const { treeWrapper, showLine } = this.props;

    return (
      <div className={`${treeWrapper}`}>
        <div className={style.storeManagementRootContainer}>
          <Row gutter={16}>
            <Col span={24}>
              <div className={style.storeDiv}>
                {TreeData.length > 0 && (
                  <Tree
                    showLine={showLine}
                    treeData={TreeData}
                    defaultSelectedKeys={
                      isStore
                        ? [selectedStoreTreeData.currentOrgId || 0]
                        : [selectedOrgTreeData.unitId || 0]
                    }
                    onSelect={treeSelect}
                    className={style.treeContainer}
                    expandedKeys={expandedKeys}
                    onExpand={onExpand}
                    autoExpandParent={autoExpandParent}
                  />
                )}
              </div>
            </Col>
          </Row>
        </div>
      </div>
    );
  }
}
