import axios from 'axios';
import { injectable } from 'inversify';
import DeviceRepository from '../domain/repositories/deviceRepository';
import { CommonPagesGeneric } from '../common/config/commonConfig';
import {
  GroupListEntity,
  DeviceListEntity,
  ScreenListEntity,
  PatrolEntity,
  DeviceRecordListItemConfig,
  DeviceFloorDistributionEntity,
  DeviceStatisticsListEntity,
  DeviceHistoryOnlineRateEntity,
  StoreListItemConfig,
  DeviceOnlineStatusEntity,
  DeviceTypeNumberEntity,
  NoticePlayPlanEntity,
} from '../domain/entities/deviceEntities';
import { OrganizationTreeListEntity } from '../domain/entities/organizationEntities';

@injectable()
export default class DeviceRepositoryImpl implements DeviceRepository {
  async requestDeviceList(
    type?: string,
    page?: number,
    size?: number,
    floor?: string,
    name?: string,
    status?: string,
    offOn?: string,
    groupIdList?: string,
  ): Promise<CommonPagesGeneric<DeviceRecordListItemConfig>> {
    const { data } = await axios.get(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device`, {
      params: {
        type,
        page,
        size,
        floor,
        name,
        status,
        offOn,
        groupIdList,
      },
    });
    return data;
  }
  async requestDel(id?: number): Promise<void> {
    await axios.delete(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/${id}`);
  }

  // 分组配置分组列表
  async requestGroupList(): Promise<GroupListEntity[]> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/group`,
    );
    return data;
  }

  // 设备导出
  async requestExport(deviceIdList: number[]): Promise<File> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/export`,
      {
        params: { deviceIdList },
        responseType: 'blob',
      },
    );
    return data;
  }

  // 删除定时设置
  async requestDelTimingSwitch(deviceIdList: number[]): Promise<void> {
    await axios.delete(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/delete-timing-switch`,
      {
        data: deviceIdList,
      },
    );
  }

  // 定时设置
  async requestTimingSwitch(id: number[], bootTime?: string, shutdownTime?: string): Promise<void> {
    await axios.post(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/timer-switch`, {
      bootTime,
      id,
      shutdownTime,
    });
  }

  // 分组配置新增
  async requestCreateGroup(groupName?: string): Promise<GroupListEntity> {
    const { data } = await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/group`,
      {
        groupName,
      },
    );
    return data;
  }

  // 分组配置删除
  async requestDelGroup(id?: number): Promise<void> {
    const { data } = await axios.delete(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/group/${id}`,
    );
    return data;
  }

  // 查询设备类型及数量
  async requestTypeNumber(): Promise<DeviceTypeNumberEntity> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/count-type-and-number`,
    );
    return data;
  }

  // 分组配置设备列表
  async requestDeviceListPage(
    page?: number,
    size?: number,
    groupIds?: string,
    type?: string,
  ): Promise<CommonPagesGeneric<DeviceListEntity>> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/group/page`,
      {
        params: {
          groupIds,
          type,
          page,
          size,
        },
      },
    );
    return data;
  }
  // 分组配置设备绑定
  async requestDeviceBind(deviceIds?: number[], groupIds?: number[], type?: string): Promise<void> {
    const { data } = await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/group/bind`,
      {
        deviceIds,
        groupIds,
        type,
      },
    );
    return data;
  }

  // 设备截图-设备截图
  async requestScreenShot(deviceId: number | undefined): Promise<void> {
    const { data } = await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/screenShot/${deviceId}`,
    );
    return data;
  }

  // 设备截图-截屏记录
  async requestScreenList(
    page?: number,
    size?: number,
    deviceId?: number,
  ): Promise<CommonPagesGeneric<ScreenListEntity>> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/screenshot/${deviceId}/list`,
      {
        params: {
          page,
          size,
        },
      },
    );
    return data;
  }

  // 设备巡查
  async requestPatrol(deviceId?: number): Promise<PatrolEntity> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/statistics/broken-network-number`,
      {
        params: {
          deviceId,
        },
      },
    );
    return data;
  }
  async requestDelBatch(deviceIdList: string[]): Promise<void> {
    await axios.delete(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/batch-delete`, {
      data: deviceIdList,
    });
  }

  async requestShutDown(deviceIdList: string[]): Promise<void> {
    await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/shutdown`,
      deviceIdList,
    );
  }

  async requestBootUp(deviceIdList: string[]): Promise<void> {
    await axios.post(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/bootup`, deviceIdList);
  }

  async requestReboot(deviceIdList: string[]): Promise<void> {
    await axios.post(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/reboot`, deviceIdList);
  }

  async requestCheckAdPlaying(deviceIdList: string[]): Promise<boolean> {
    try {
      await axios.post(
        `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/advertisement/check-ad-playing`,
        deviceIdList,
      );
      return false;
    } catch (error) {
      return true;
    }
  }

  // 修改设备信息
  async requsetEdit(query: DeviceRecordListItemConfig): Promise<DeviceRecordListItemConfig> {
    const { data } = await axios.put(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device`,
      query,
    );
    return data;
  }

  // 批量修改设备信息
  async requsetBatchEdit(
    deviceIdList: string[],
    groupIdList?: string[],
    projectStoreId?: string,
  ): Promise<void> {
    const { data } = await axios.put(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/batch-update`,
      {
        deviceIdList,
        groupIdList,
        projectStoreId,
      },
    );
    return data;
  }

  // 根据设备id列表， 查询设备所属组织的所有门店
  async requestSelectStoreListByDeviceOrgId(
    deviceIdList: number[],
  ): Promise<StoreListItemConfig[]> {
    const { data } = await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/store/selectStoreListByDeviceOrgId`,
      deviceIdList,
    );
    return data;
  }

  // 获取设备详情
  async requestDeviceDetails(deviceId?: number): Promise<DeviceRecordListItemConfig> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/${deviceId}`,
    );
    return data;
  }
  // 获取项目门店列表
  async requestStoreList(
    page: number,
    size: number,
    name?: string,
    type?: string,
    cityCode?: string,
    categoryCode?: string,
  ): Promise<CommonPagesGeneric<StoreListItemConfig>> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/store/page`,
      {
        params: {
          page,
          size,
          name,
          type,
          cityCode,
          categoryCode,
        },
      },
    );
    return data;
  }

  // 新增设备
  async requestCreate(query: DeviceRecordListItemConfig): Promise<DeviceRecordListItemConfig> {
    const { data } = await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device`,
      query,
    );
    return data;
  }
  // 删除项目门店单条数据
  async requestStoreDel(id?: number): Promise<void> {
    const { data } = await axios.delete(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/store/${id}`,
    );
    return data;
  }

  // 是否关联设备
  async requestIsRelate(storeId?: number): Promise<void> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/store/check-device-by-store`,
      {
        params: {
          storeId,
        },
      },
    );
    return data;
  }

  // 当前在线/离线设备及在线率统计
  async requestDeviceOnlineStatus(): Promise<DeviceStatisticsListEntity[]> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/statistics/total`,
    );
    return data;
  }

  // 获取当前设备类型分布
  async requestDeviceDistributionData(): Promise<DeviceStatisticsListEntity[]> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/statistics/type`,
    );
    return data;
  }

  // 获取当前设备广告播放利用率
  async requestDevicePlayRateData(deviceType?: string): Promise<DeviceStatisticsListEntity[]> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/statistics/advertising`,
      {
        params: {
          deviceType,
        },
      },
    );
    return data;
  }

  // 获取设备楼层分布数据
  async requestDeviceFloorDistributionData(): Promise<DeviceFloorDistributionEntity[]> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/statistics/floor`,
    );
    return data;
  }

  // 获取今日设备在线情况
  async requestDeviceTodayOnlineData(): Promise<DeviceOnlineStatusEntity[]> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/statistics/hour`,
    );
    return data;
  }

  // 获取历史设备在线率
  async requestDeviceHistoryOnlineRate(
    dayType?: number,
    beginDate?: string,
    endDate?: string,
    deviceType?: string,
  ): Promise<DeviceHistoryOnlineRateEntity[]> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/statistics/day`,
      {
        params: {
          dayType,
          beginDate,
          endDate,
          deviceType,
        },
      },
    );
    return data;
  }

  // 设备离线导出
  async deviceOfflineExport(hours?: number, beginDate?: string, endDate?: string): Promise<File> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/statistics/off-line-export`,
      {
        params: {
          hours,
          beginDate,
          endDate,
        },
        responseType: 'blob',
      },
    );
    return data;
  }

  // 设备在线导出
  async deviceOnlineExport(
    dayType?: number,
    beginDate?: string,
    endDate?: string,
    deviceType?: string,
  ): Promise<File> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/statistics/online-export`,
      {
        params: {
          dayType,
          beginDate,
          endDate,
          deviceType,
        },
        responseType: 'blob',
      },
    );
    return data;
  }

  // 新增项目门店
  async addStore(params: StoreListItemConfig): Promise<StoreListItemConfig> {
    const { data } = await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/store`,
      params,
    );
    return data;
  }

  // 编辑项目门店
  async editStore(query: StoreListItemConfig): Promise<StoreListItemConfig> {
    const { data } = await axios.put(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/store`,
      query,
    );
    return data;
  }

  // 获取设备详情
  async requestStoreDetails(storeId?: number): Promise<StoreListItemConfig> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/store/${storeId}`,
    );
    return data;
  }

  // 获取组织列表数据
  async requestOrganizationList(): Promise<OrganizationTreeListEntity> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/device/unit/query-org-tree`,
    );
    return data;
  }

  // 通知播放计划
  async requestNoticePlayPlan(
    deviceId: number,
    page: number,
    size: number,
    status?: string,
  ): Promise<CommonPagesGeneric<NoticePlayPlanEntity>> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/playlist/notice`,
      {
        params: {
          deviceId,
          page,
          size,
          status,
        },
      },
    );
    return data;
  }
}
