  public beforeUpload(file: RcFile, fileType: RcFile[], resolution: string): boolean {
    console.log(file, fileType, resolution);
    // 拿到视频的时长
    const videoUrl = URL.createObjectURL(file);
    console.log(videoUrl, 'videoUrl');
    const videoObj = document.createElement('video');
    videoObj.preload = 'metadata';
    videoObj.src = videoUrl;
    videoObj.onloadedmetadata = (): void => {
      URL.revokeObjectURL(videoUrl);
      const times = Math.round(videoObj.duration);
      this.videoTime = times;
      // 拿到视频的分辨率
      if (resolution !== `${videoObj.videoWidth}*${videoObj.videoHeight}`) {
        Modal.confirm({
          title: '提示',
          content: '当前上传的素材分辨率与设备分辨率不符，是否继续上传?',
          icon: undefined,
          onOk() {
            //
          },
        });
      }
      console.log(videoObj.videoWidth, videoObj.videoHeight);
    };
    // 拿到图片分辨率
    const reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = (): void => {
      const image = new Image();
      image.src = (reader.result as string) || '';
      image.onload = (): void => {
        if (resolution !== `${image.width}*${image.width}`) {
          Modal.confirm({
            title: '提示',
            content: '当前上传的素材分辨率与设备分辨率不符，是否继续上传?',
            icon: undefined,
            onOk() {
              //
            },
          });
        }
        // this.imgWidth = image.width;
        // this.imgHeight = image.height;
        console.log(image.width, image.height); //文件像素大小
      };
    };

    console.log(file.type.split('/')[0]);
    const isLt = [];
    if (file.type.split('/')[0] === 'video') {
      const isLt20M = file.size / 1024 / 1024 > 20;
      if (isLt20M) {
        message.error('上传视频不能超过20M');
        isLt.push(1);
      }
    }
    if (file.type.split('/')[0] === 'image') {
      const isLt2M = file.size / 1024 / 1024 > 2;
      if (isLt2M) {
        message.error('上传图片不能超过2M');
        isLt.push(2);
      }
    }
    return isLt.length === 0;
  }
