  // 切换设备
  public switchDevice = (value: string): void => {
    this.deviceContent = value;
    // this.advertingListDataSource = [];
    // this.cashierListDataSource = [];
    // this.ledListDataSource = [];
    // this.storeNameList = [];
    this.specificDevice = [];
    if(this.wayTypeAd === 'project' || this.wayTypeLed === 'project' || this.wayTypeCa === 'project'){
      if(value === 'CASHIER'){
        this.getGcashierDevices(value)
      }else{
        this.getSpecificDevices(value);
      }
    }else{
      this.getSpecificGroupDevices(value);
    }

    // 创建 和编辑的时候全部设备调用接口
    if (this.device === 'all') {
      this.setSelectAllByTenantId(value);
    }

    // 编辑的时候 部分设备和调用这个列表
    if (this.device === 'part' && this.modalType === 'edit') {
      this.getEditDeviceList(value, this.DetailData.adId);
    }
  };

  // 切换设备范围
  public deviceChange = (e: RadioChangeEvent): void => {
    this.device = e.target.value;
    this.deviceContent = 'ADMACHINE';
    this.advertingListDataSource = [];
    this.cashierListDataSource = [];
    this.ledListDataSource = [];
    this.storeNameList = [];
    // this.wayType = 'project';
    // 点全部设备的时候调用接口（不论是新增或者编辑）
    if (e.target.value === 'all') {
      this.setSelectAllByTenantId(this.deviceContent);
    }

    // 点部分设备并且是创建的时候，清除数据
    if (e.target.value === 'part' && this.modalType === 'create') {
      this.advertingListDataSource = [];
    }
    // 点部分设备并且是编辑的时候调用接口
    if (e.target.value === 'part' && this.modalType === 'edit') {
      this.getEditDeviceList(this.deviceContent, this.DetailData.adId);
    }
  };

