import React, { ReactText } from 'react';
import { observer } from 'mobx-react';
import { ColumnProps } from 'antd/lib/table';
import { FormInstance } from 'antd/lib/form';
import { Table, Select, Button, Form, Row, Input, Tooltip, Modal, Tag, Col } from 'antd';
import OrganizationTree from '../../../common/components/organizationTree';
import style from './style.less';
import { DEVICE_IDENTIFIER } from '../../../constants/identifiers';
import DI from '../../../inversify.config';
import StoreListViewModel, { StoreListDataConfig } from './viewModel';
import StoreListAdd from '../../../assets/images/project_icon_add.svg';
import CreateProjectModalViewModel from './createStoreModal/viewModal';
import { ModalStatus } from '../../../common/config/commonConfig';
import CreateProjectModal from './createStoreModal/index';
import { OrganizationListEntity } from '../../../domain/entities/organizationEntities';
import userlistEditInfo from '../../../assets/images/edit_info_icon.svg';
import userlistDelIfo from '../../../assets/images/del_info_icon.svg';

interface StoreListProps {}
interface StoreListState {
  // 项目门店表格列
  StoreListColumns: ColumnProps<StoreListDataConfig>[];
  selectedRowKeys: React.Key[];
}

@observer
export default class StoreList extends React.Component<StoreListProps, StoreListState> {
  private viewModel = DI.DIContainer.get<StoreListViewModel>(
    DEVICE_IDENTIFIER.PROJECT_STORE_VIEW_MODEL,
  );
  private createProjectModalViewModel = DI.DIContainer.get<CreateProjectModalViewModel>(
    DEVICE_IDENTIFIER.CREATE_PROJECT_MODAL_VIEW_MODEL,
  );
  private formRef = React.createRef<FormInstance>();

  constructor(props: StoreListProps) {
    super(props);
    this.state = {
      StoreListColumns: [],
      selectedRowKeys: [],
    };
  }

  componentDidMount(): void {
    const { getStoreList, getCategory, getType } = this.viewModel;
    this.getStoreData();
    getStoreList();
    getCategory();
    getType();
  }

  private resetForm = (): void => {
    this.formRef.current?.resetFields();
  };

    // 删除列表单条数据
    private deleteStore = async (data: StoreListDataConfig): Promise<void> => {
      const { isRelate, deleteStore } = this.viewModel;
      await isRelate(data)
        .then(() => {
          Modal.confirm({
            title: '删除',
            content: '删除项目/门店将失效并无法恢复，确认删除?',
            icon: undefined,
            onOk() {
              deleteStore(data);
            },
          });
        })
        .catch(() => {
          Modal.confirm({
            title: '删除',
            content: '该项目/门店下已关联了设备无法删除，请将该项目/门店下的设备进行转移到其他项目/门店下，才能删除。',
            icon: undefined,
          });
        });
    };

    // 打开新增/编辑弹窗
    private openModal = async (
      statusType: ModalStatus,
      storesType: string,
      itemData?: StoreListDataConfig,
    ): Promise<void> => {
      const { setStoreItemData } = this.viewModel;
      setStoreItemData(statusType, storesType, itemData, this.createProjectModalViewModel);
    };
  // 构造列表结构
  private getStoreData = (): void => {
    this.setState({
      StoreListColumns: [
        {
          title: '名称',
          key: 'name',
          align: 'left',
          width: '15%',
          render: (record: StoreListDataConfig): JSX.Element => (
            <Button type="link" size="small">
              {record.name}
            </Button>
          ),
        },
        {
          title: '类型',
          dataIndex: 'type',
          key: 'type',
          align: 'left',
          width: '10%',
          ellipsis: true,
          render: (value: string): JSX.Element => (
            value === 'PROJECT' ? (
              <div className={style.projectType}>项目</div>
            ) : (
              <div className={style.storesType}>门店</div>
            )),
        },
        {
          title: '种类',
          dataIndex: 'category',
          key: 'category',
          align: 'left',
          width: '10%',
          ellipsis: true,
          render: (value: string): JSX.Element => (
            value === '' ? <div>--</div> : <div>{value}</div>),
        },
        {
          title: '城市',
          dataIndex: 'city',
          key: 'city',
          align: 'left',
          width: '10%',
          ellipsis: true,
        },
        {
          title: '状态',
          dataIndex: 'status',
          key: 'status',
          align: 'left',
          width: '10%',
          render: (value: string): JSX.Element => (
            <div>
              {value ? (
                <Tag color="green" className={style.statusStyle}>
                  <b className={style.openStatus} />
                  开启
                </Tag>
              ) : (
                <Tag color="red" className={style.statusStyle}>
                  <b className={style.disabledStatus} />
                  禁用
                </Tag>
              )}
            </div>
          ),
        },
        {
          title: '地址/门牌号',
          dataIndex: 'address',
          key: 'address',
          align: 'left',
          width: '20%',
          ellipsis: true,
          render: (value: string): JSX.Element => (
            <Tooltip placement="topLeft" title={value}>{value}</Tooltip>
          ),
        },
        {
          title: '营业时间',
          key: 'runningtime',
          align: 'left',
          ellipsis: true,
          width: '15%',
          render: (record: StoreListDataConfig): JSX.Element => (
            <div>
              {`${record.beginBusinessHours}-${record.endBusinessHours}`}
            </div>
          ),
        },
        {
          title: '操作',
          key: 'operator',
          align: 'left',
          fixed: 'right',
          width: '20%',
          render: (record: StoreListDataConfig): JSX.Element => (
            <div className={style.operatorContainer}>
              <Tooltip title="编辑">
                <button
                  type="button"
                  className={style.opertionBtn}
                  onClick={(): Promise<void> => (
                    this.openModal(
                      ModalStatus.Edit,
                      record.type === '项目' ? 'project' : 'store',
                      record,
                   ))}
                >
                  <img src={userlistEditInfo} alt="" />
                </button>
              </Tooltip>
              <Tooltip title="删除">
                <button
                  type="button"
                  className={style.opertionBtn}
                  onClick={(): Promise<void> => this.deleteStore(record)}
                >
                  <img src={userlistDelIfo} alt="" />
                </button>
              </Tooltip>
            </div>
          ),
        },
      ],
    });
  };

  // 多选行数据
  public onSelectChange = (selectedRowKeys: ReactText[]): void => {
    this.setState({ selectedRowKeys });
  };

  // TODO
  public getCurrentSelected = (e: OrganizationListEntity): void => {
    console.log(e);
  };

  public render(): JSX.Element {
    const {
      storeListDataSource,
      queryParams,
      pageChange,
      sizeChange,
      selectType,
      selectCategory,
      onFinish,
      storeListData,
      categoryData,
      typeData,
    } = this.viewModel;
    const { StoreListColumns, selectedRowKeys } = this.state;

    return (
      <div className={style.projectListrootContainer}>
        <div className={style.deviceNav}>
          <OrganizationTree
            treeWrapper={style.treeWrapper}
            showLine={{ showLeafIcon: false }}
            getCurrentSelectedInfo={(e: OrganizationListEntity): void => {
              this.getCurrentSelected(e);
            }}
          />
        </div>
        <div className={style.tableContainer}>
          <Row gutter={24} className={style.searchArea}>
            <Col span={4}>
              <Form.Item name="type">
                <Select
                  bordered={false}
                  defaultValue="all"
                  onChange={(e: string): void => selectType(e)}
                >
                  <Select.Option value="all">所有类型</Select.Option>
                  {typeData &&
                    typeData.map((item, index) => (
                      <Select.Option value={item.value || ''} key={index}>
                        {item.meaning}
                      </Select.Option>
                    ))}
                </Select>
              </Form.Item>
            </Col>
            <Col span={4}>
              <Form.Item name="categoryCode">
                <Select
                  bordered={false}
                  defaultValue="all"
                  onChange={(e: string): void => selectCategory(e)}
                >
                  <Select.Option value="all">所有种类</Select.Option>
                  {categoryData &&
                    categoryData.map((item, index) => (
                      <Select.Option value={item.value || ''} key={index}>
                        {item.meaning}
                      </Select.Option>
                    ))}
                </Select>
              </Form.Item>
            </Col>
            <Col span={16}>
              <Form onFinish={onFinish} ref={this.formRef}>
                <Row gutter={24}>
                  <Col span={7}>
                    <Form.Item name="name">
                      <Input placeholder="项目/ 门店名称" />
                    </Form.Item>
                  </Col>
                  <Col span={7}>
                    <Form.Item name="cityCode">
                      <Input placeholder="城市名称" />
                    </Form.Item>
                  </Col>
                  <Col span={10}>
                    <div className={style.buttons}>
                      <Button onClick={this.resetForm}>重置</Button>
                      <Button type="primary" htmlType="submit">
                        查询
                      </Button>
                    </div>
                  </Col>
                </Row>
              </Form>
            </Col>
          </Row>
          <div className={style.buttonsContainer}>
            <Button
              type="primary"
              style={{ marginRight: '16px' }}
              onClick={(): Promise<void> => (
                this.openModal(ModalStatus.Creat, 'project', {})
              )}
            >
              <img src={StoreListAdd} alt="" />
              <span>新增项目</span>
            </Button>
            <Button
              type="primary"
              style={{ marginRight: '16px' }}
              onClick={(): Promise<void> => (
                this.openModal(ModalStatus.Creat, 'store', {})
              )}
            >
              <img src={StoreListAdd} alt="" />
              <span>新增门店</span>
            </Button>
          </div>

          <Table<StoreListDataConfig>
            pagination={{
              size: 'small',
              showSizeChanger: true,
              showQuickJumper: true,
              total: storeListData.totalElements || 0,
              pageSize: queryParams.size,
              current: queryParams.page + 1,
              onChange: (page: number, pageSize?: number): void => pageChange(page, pageSize),
              onShowSizeChange: (current: number, size: number): void => sizeChange(current, size),
            }}
            className={style.table}
            rowClassName={style.tableRow}
            rowSelection={{
              fixed: true,
              columnWidth: '50px',
              selectedRowKeys,
              onChange: this.onSelectChange,
            }}
            columns={StoreListColumns}
            dataSource={storeListDataSource}
          />
        </div>
        <CreateProjectModal />
      </div>
    );
  }
}
