import React from 'react';
import { observer } from 'mobx-react';
import {
  Modal,
  Form,
  Button,
  Input,
  DatePicker,
  Radio,
  Tooltip,
  Steps,
  Checkbox,
  Row,
  Col,
  Select,
  Upload,
  Tabs,
  Cascader,
  Table,
  TimePicker,
  FormInstance,
  InputNumber,
} from 'antd';
import { ColumnProps } from 'antd/lib/table';
import moment from 'moment';
import DI from '../../../../inversify.config';
import { ADVERTISEMENT_IDENTIFIER, DEFAULT_PAGE_IDENTIFIER } from '../../../../constants/identifiers';
import CreatAdvertisementModalViewModel from './viewModel';
import {
  AdvertisementDeviceListEntity,
} from '../../../../domain/entities/advertisementEntities';
import style from './style.less';
import timeTooips from '../../../../assets/images/time_tooips.svg';
import closeIcon from '../../../../assets/images/close_icon_normal.svg';
import addIcon from '../../../../assets/images/circle_add_icon.svg';
import deleteIcon from '../../../../assets/images/circle_delete_icon.svg';
import uploadIcon from '../../../../assets/images/upload.svg';
import uploadRecord from '../../../../assets/images/upload_record.svg';
import resultsIcon from '../../../../assets/images/results_icon.svg';
import deleteTable from '../../../../assets/images/delete_icon.svg';

import MaterialPreviewModal from '../../../../common/components/materialPreviewModal/index'
import AdvertisementListViewModel from '../advertisementList/viewModel';
import UploadHistoryRecordModal from '../../advertisement/uploadHistoryRecordModal/index';
import UploadHistoryRecordModalViewModel from '../../advertisement/uploadHistoryRecordModal/viewModel';
import utils from '../../../../utils/index';

const { Step } = Steps;

interface AdvertingProps {}
interface AdvertingState {
  //列表表格列头
  advertingListColumns: ColumnProps<AdvertisementDeviceListEntity>[];
  cashierListColumns: ColumnProps<AdvertisementDeviceListEntity>[];
}

@observer
export default class CreatAdvertisementModal
extends React.Component <AdvertingProps, AdvertingState> {
  private creatAdvertisementModalViewModel = DI.DIContainer.get<CreatAdvertisementModalViewModel>(
    ADVERTISEMENT_IDENTIFIER.ADVERTISEMENT_CREATADVERTISEMENT_VIEW_MODEL,
  );

  private advertisementListViewModel = DI.DIContainer.get<AdvertisementListViewModel>(
    ADVERTISEMENT_IDENTIFIER.ADVERTISEMENT_LIST_VIEW_MODEL,
  );

  private uploadHistoryRecordModalViewModel = DI.DIContainer.get<UploadHistoryRecordModalViewModel>(
    DEFAULT_PAGE_IDENTIFIER.UPLOAD_HISTORY_RECORD_MODAL_VIEW_MODEL,
  );

  private formRef = React.createRef<MaterialPreviewModal>();
  private getRef = React.createRef<FormInstance>();
  private adRef = React.createRef<FormInstance>();
  private deviceRef = React.createRef<FormInstance>();

  constructor(props: AdvertingProps) {
    super(props);
    this.state = {
      advertingListColumns: [],
      cashierListColumns: [],
    };
  }

  componentDidMount(): void {
    const { deleteDevice } = this.creatAdvertisementModalViewModel;
    // getAdvertisingList();
    this.setState({
      advertingListColumns: [
        {
          title: '项目/门店名称',
          dataIndex: 'storeName',
          key: 'storeName',
          align: 'left',
        },
        {
          title: '所在楼层',
          dataIndex: 'floor',
          key: 'floor',
          align: 'left',
        },
        {
          title: '设备名称',
          dataIndex: 'deviceName',
          key: 'deviceName',
          align: 'left',
        },
        {
          title: '组名名称',
          dataIndex: 'groupName',
          key: 'groupName',
          align: 'left',
        },
        {
          title: '操作',
          key: 'action',
          align: 'left',
          fixed: 'right',
          width: '8%',
          render: (record): JSX.Element => (
            <div>
              <Tooltip title="删除">
                <button
                  type="button"
                  className={style.opertionBtn}
                  onClick={(): void => deleteDevice(record)}
                >
                  <img src={deleteTable} alt="" />
                </button>
              </Tooltip>
            </div>
          ),
        },
      ],
      cashierListColumns: [
        {
          title: '项目/门店名称',
          dataIndex: 'storeName',
          key: 'storeName',
          align: 'left',
        },
        {
          title: '品牌业态',
          dataIndex: 'brandFormat',
          key: 'brandFormat',
          align: 'left',
        },
        {
          title: '点位品牌名称',
          dataIndex: 'deviceName',
          key: 'deviceName',
          align: 'left',
        },
        {
          title: '组名名称',
          dataIndex: 'groupName',
          key: 'groupName',
          align: 'left',
        },
        {
          title: '操作',
          key: 'action',
          align: 'left',
          width: '8%',
          fixed: 'right',
          render: (record): JSX.Element => (
            <div>
              <Tooltip title="删除">
                <button
                  type="button"
                  className={style.opertionBtn}
                  onClick={(): void => deleteDevice(record)}
                >
                  <img src={deleteTable} alt="" />
                </button>
              </Tooltip>
            </div>
          ),
        },
      ],
    });
  }
  // 打开历史上传记录modal
  private historyUpload = async (type?: string, index?: number): Promise<void> => {
    const {
      getUploadRecordListData,
      setUploadHistoryRecordModalVisible,
    } = this.uploadHistoryRecordModalViewModel;
    await getUploadRecordListData();
    setUploadHistoryRecordModalVisible(type, index);
  }

  private labelTooips(): JSX.Element {
    return (
      <div>
        霸屏情况
        <Tooltip placement="top" title="提示：若选择霸屏则其他广告暂停播放，只播该条广告内容。">
          <img className={style.timeImg} src={timeTooips} alt="" />
        </Tooltip>
      </div>
    );
  }

  // 设备content
  private tabsContent(): JSX.Element {
    const {
      setDevice,
      setWayType,
      specificDevice,
      device,
      wayTypeAd,
      wayTypeCa,
      wayTypeLed,
      deviceContent,
      optionsDevice,
      advertingListDataSource,
      cashierListDataSource,
      ledListDataSource,
      storeNameList,
    } = this.creatAdvertisementModalViewModel;
    const { advertingListColumns, cashierListColumns } = this.state;

    // 去重后展示
    const dataSource = advertingListDataSource.filter((item, index) => {
      const idx = advertingListDataSource.findIndex((i) => {
        return item.id === i.id;
      });
      return index === idx;
    });

    const cashierDataSource = cashierListDataSource.filter((item, index) => {
      const idx = cashierListDataSource.findIndex((i) => {
        return item.id === i.id;
      });
      return index === idx;
    });

    const ledDataSource = ledListDataSource.filter((item, index) => {
      const idx = ledListDataSource.findIndex((i) => {
        return item.id === i.id;
      });
      return index === idx;
    });

    // 切换的时候级联清空
    this.deviceRef.current?.setFieldsValue({ device: specificDevice });

    // 切换的时候， 设置当前的选择方式
    if(deviceContent === 'ADMACHINE'){
      this.deviceRef.current?.setFieldsValue({ way: wayTypeAd });
    }else if(deviceContent === 'CASHIER'){
      this.deviceRef.current?.setFieldsValue({ way: wayTypeCa });
    }else if(deviceContent === 'LED'){
      this.deviceRef.current?.setFieldsValue({ way: wayTypeLed });
    }
    return (
      <div>
        {
          device === 'part' && (
            <Col span={24}>
              <Row>
                <Col span={11} className={style.screening}>
                  <Form.Item label="选择方式" name="way" initialValue={wayTypeAd}>
                    <Select
                      placeholder="请选择选择方式"
                      style={{ width: '200px' }}
                      onChange={setWayType}
                      getPopupContainer={(triggerNode): HTMLElement => (
                        triggerNode.parentElement || triggerNode
                      )}
                    >
                      <Select.Option value="project">按项目/门店选择</Select.Option>
                      <Select.Option value="group">按分组选择</Select.Option>
                    </Select>
                  </Form.Item>
                </Col>
                <Col span={2} />
                <Col span={11} className={style.screening}>
                  <Form.Item
                    label="具体设备"
                    name="device"
                    className={style.scope}
                    // initialValue={specificDevice}
                  >
                    <Cascader
                      style={{ width: '200px' }}
                      multiple
                      showSearch
                      value={specificDevice}
                      onChange={setDevice}
                      options={optionsDevice}
                      maxTagCount="responsive"
                      placeholder="请选择具体设备"
                      // getPopupContainer={(triggerNode): HTMLElement => (
                      //   triggerNode.parentElement || triggerNode
                      // )}
                    />
                  </Form.Item>
                </Col>
              </Row>
            </Col>
          )
        }
        <div className={style.result}>
          <img src={resultsIcon} alt="" />
          <span>选择结果：{storeNameList.length}个项目/门店，
          {dataSource.length + cashierDataSource.length + ledDataSource.length }台设备</span>
        </div>
        <div className={style.tableContent}>
          <Table
            pagination={{
              size: 'small',
              showSizeChanger: false,
              showQuickJumper: true,
              pageSize: 3,
            }}
            scroll={{ x: 900 }}
            className={style.table}
            rowClassName={style.tableRow}
            columns={deviceContent === 'CASHIER' ? cashierListColumns : advertingListColumns}
            dataSource={this.switchDataSource(deviceContent)}
          />
        </div>
      </div>
    );
  }

  public switchDataSource = (deviceType: string): AdvertisementDeviceListEntity[] => {
    const { advertingListDataSource, cashierListDataSource, ledListDataSource } = this.creatAdvertisementModalViewModel;
    switch (deviceType) {
      case 'ADMACHINE':
        return advertingListDataSource;
      case 'CASHIER':
        return cashierListDataSource;
      case 'LED':
        return ledListDataSource;
      default:
        return advertingListDataSource;
    }
  };
  // 下一步
  private submitForm = (): void => {
    const {
      setCurrent,
    } = this.creatAdvertisementModalViewModel;
    setCurrent('next')
    this.getRef.current?.submit()
  }

  private handlePreviewFile = (file: File | Blob): PromiseLike<string> => {
    return new Promise((resolve) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = (): void => {
        const url = URL.createObjectURL(file)
        if (file.type.split('/')[0] === 'video') {
           utils.getFramesUrl(url).then((res) => {
            resolve(res)
          })
        } else {
          resolve(url)
        }
    }
  })
}

  public render(): JSX.Element {
    const {
      modalType,
      //条件配置
      formOnFinish,
      configConditions,
      advertisingModalVisible,
      setAdvertisingModalVisible,
      current,
      setCurrent,
      weeks,
      cycleChange,
      optionsCheck,
      timeSlot,
      setTimeSlot,
      //上传素材
      uploadFormProps,
      setUploadType,
      uploadType,
      fileList,
      uploadChange,
      setUploadTime,
      setImagePreview,
      initBackgroundItem,
      imgUrl,
      uploadTime,
      modalIndex,
      beforeUpload,
      // getfileUrl,
      //选择设备
      device,
      deviceChange,
      switchDevice,
      saveRelease,
      resolutionList,
      deviceContent,
      //快码
      cycleCode,
      advertisementLevelCode,
      materialData,
      materialIndex,
    } = this.creatAdvertisementModalViewModel;
    const steps = [
      {
        title: '配置条件',
        content: 'First-content',
      },
      {
        title: '选择设备',
        content: 'Second-content',
      },
      {
        title: '上传素材',
        content: 'Last-content',
      },
    ];

    if (materialIndex !== undefined) {
      const data = materialData.type === 'jpg' || materialData.type === 'jpeg' || materialData.type === 'png' ? 'image' : 'video'
      this.adRef.current?.setFieldsValue({ [`type${materialIndex}`]: data });
    }

    return (
      <Modal
        visible={advertisingModalVisible}
        width={750}
        closable={false}
        footer={null}
        wrapClassName={style.exportModalContainer}
        destroyOnClose
      >
        <div className={style.exportModalContent}>
          <div className={style.modalHeader}>
            {modalType === 'edit' ? '编辑广告' : '发布广告'}
            <Button type="text" onClick={(): void => setAdvertisingModalVisible()}>
              <img src={closeIcon} alt="" />
            </Button>
          </div>
          <Steps current={current}>
            {steps.map((item) => (
              <Step key={item.title} title={item.title} />
            ))}
          </Steps>
          <div>
            {current === 0 && (
              <div className={style.stepsContent}>
                <Form
                  onFinish={(values): void => {
                    formOnFinish(values, this.advertisementListViewModel);
                  }}
                  labelCol={{ span: 5 }}
                  wrapperCol={{ span: 21 }}
                  ref={this.getRef}
                >
                  <div>
                    <Form.Item
                      label="广告名称"
                      name="advertisement"
                      rules={[{ required: true }]}
                      initialValue={configConditions.advertisement}
                    >
                      <Input placeholder="请输入广告名称" />
                    </Form.Item>

                    <Form.Item
                      label="播放日期"
                      name="playTime"
                      rules={[{ required: true, message: '请选择日期区间' }]}
                      initialValue={configConditions.playTime}
                    >
                      <DatePicker.RangePicker format="YYYY-MM-DD" style={{ width: '100%' }} />
                    </Form.Item>

                    <Form.Item
                      label="具体广告"
                      name="advert"
                      rules={[{ required: true, message: '请选择具体广告' }]}
                      className={style.cycle}
                      initialValue={configConditions.advert}
                    >
                      <Radio.Group onChange={cycleChange}>
                        {cycleCode.map((item) => {
                          return (
                            <Radio value={item.value}>{item.meaning}</Radio>
                          )
                        })}
                      </Radio.Group>
                    </Form.Item>
                    {weeks === 'WEEK' && (
                      <div className={style.weeks}>
                        <Form.Item
                          name="weeks"
                          rules={[{ required: true, message: '请选择每周的具体周值' }]}
                          initialValue={configConditions.weeks}
                        >
                          <Checkbox.Group
                            options={optionsCheck}
                          />
                        </Form.Item>
                      </div>
                    )}
                    <Form.List name="doublePointsRulesFormList" initialValue={configConditions.doublePointsRulesFormList}>
                      {(fields, { add, remove }): JSX.Element => (
                        <>
                          <Row className={style.ruleList} align="middle" gutter={8}>
                            <Col span={24}>
                              <Form.Item
                                label="播放时段"
                                labelCol={{ span: 5 }}
                                wrapperCol={{ span: 21 }}
                                // name="timeOn"
                                rules={[{ required: true, message: '请选择播放时段' }]}
                              >
                                <TimePicker.RangePicker
                                  value={JSON.parse(JSON.stringify(timeSlot))[0] ? [
                                    moment(timeSlot[0], 'HH:mm:ss'),
                                    moment(timeSlot[1], 'HH:mm:ss'),
                                  ] : undefined}
                                  onChange={setTimeSlot}
                                  style={{ width: '100%' }}
                                  format="HH:mm:ss"
                                  placeholder={['开始时间', '结束时间']}
                                  getPopupContainer={(triggerNode): HTMLElement => (
                                    triggerNode.parentElement || triggerNode
                                  )}
                                />
                              </Form.Item>
                            </Col>
                            <Col span={3}>
                              <Button
                                type="text"
                                onClick={(): void => {
                                  add();
                                }}
                              >
                                <img src={addIcon} alt="" />
                              </Button>
                            </Col>
                          </Row>
                          {fields.map(({ key, name, fieldKey, ...restField }) => (
                            <Row key={key} className={style.ruleList} align="middle" gutter={8}>
                              <Col span={5} />
                              <Col span={19}>
                                <Form.Item
                                  {...restField}
                                  name={[name, 'time']}
                                  fieldKey={[fieldKey, 'time']}
                                >
                                  <TimePicker.RangePicker
                                    style={{ width: '393px' }}
                                    format="HH:mm:ss"
                                    placeholder={['开始时间', '结束时间']}
                                    getPopupContainer={(triggerNode): HTMLElement => (
                                      triggerNode.parentElement || triggerNode
                                    )}
                                  />
                                </Form.Item>
                              </Col>
                              <Col span={3}>
                                <Button
                                  type="text"
                                  onClick={(): void => {
                                    remove(name);
                                  }}
                                >
                                  <img src={deleteIcon} alt="" />
                                </Button>
                              </Col>
                            </Row>
                          ))}
                        </>
                      )}
                    </Form.List>

                    <Form.Item
                      label={this.labelTooips()}
                      name="screen"
                      className={style.cycle}
                      initialValue={configConditions.screen}
                    >
                      <Radio.Group>
                        {advertisementLevelCode.map((item) => {
                            return (
                              <Radio value={item.value}>{item.meaning}</Radio>
                            )
                          })}
                      </Radio.Group>
                    </Form.Item>
                  </div>
                  <div className={style.bottomButton}>
                    <Button
                      style={{ marginRight: '24px' }}
                      onClick={(): void => setAdvertisingModalVisible()}
                    >
                      取消
                    </Button>
                    {current < steps.length - 1 && (
                      <Button type="primary" onClick={this.submitForm} style={{ marginRight: '24px' }}>
                        下一步
                      </Button>
                    )}
                    <Button
                      type="primary"
                      htmlType="submit"
                    >
                      保存
                    </Button>
                  </div>
                </Form>
              </div>
            )}
            {current === 1 && (
              <div>
                <Form ref={this.deviceRef}>
                  <div className={style.device}>
                    <Form.Item
                      label="发送范围"
                      name="advert"
                      className={style.scope}
                      initialValue={device}
                    >
                      <Radio.Group onChange={deviceChange}>
                        <Radio value="part">部分设备</Radio>
                        <Radio value="all">全部设备</Radio>
                      </Radio.Group>
                    </Form.Item>
                  </div>
                  <div className={style.tabsContainer}>
                    <Tabs defaultActiveKey="ADMACHINE" onChange={switchDevice} activeKey={deviceContent}>
                      <Tabs.TabPane tab="广告机" key="ADMACHINE">
                        {this.tabsContent()}
                      </Tabs.TabPane>
                      <Tabs.TabPane tab="收银机" key="CASHIER">
                        {this.tabsContent()}
                      </Tabs.TabPane>
                      <Tabs.TabPane tab="LED" key="LED">
                        {this.tabsContent()}
                      </Tabs.TabPane>
                    </Tabs>
                  </div>
                  <div className={style.bottomButton}>
                    <Button
                      style={{ marginRight: '24px' }}
                      onClick={(): void => setAdvertisingModalVisible()}
                    >
                      取消
                    </Button>
                    {current > 0 && (
                      <Button
                        type="primary"
                        style={{ marginRight: '24px' }}
                        onClick={(): void => setCurrent('prev')}
                      >
                        上一步
                      </Button>
                    )}
                    {current < steps.length - 1 && (
                      <Button type="primary" onClick={(): void => setCurrent('next')} style={{ marginRight: '24px' }}>
                        下一步
                      </Button>
                    )}
                    <Button
                      type="primary"
                      onClick={(): void => saveRelease('device', '', this.advertisementListViewModel)}
                    >
                      保存
                    </Button>
                  </div>
                </Form>
              </div>
            )}
            {current === 2 && (
              <div className={style.uploadContent}>
                <div className={style.uploadTitle}>
                  <div>1、支持图片、视频格式上传，包括不限于：MP4、WAV、PNG、JPG等。</div>
                  <div>2、视频格式大小在20M内，图片格式大小在2M内。</div>
                  <div>3、可上传5-20秒内的素材。</div>
                  <div>4、为了更好的播放效果，请选择分辨率与设备分辨率相同的素材。</div>
                </div>
                <Row>
                  {resolutionList.map((item, index) => {
                      return (
                        <Col span={11} className={style.upload}>
                          <Form ref={this.adRef}>
                            <Form.Item label="上传类型" name={`type${index}`} rules={[{ required: true }]} initialValue={uploadType[index] || undefined}>
                              <Select
                                placeholder="请选择上传类型"
                                style={{ width: '200px' }}
                                onChange={(e: string): void => setUploadType(e, index)}
                              >
                                <Select.Option value="image">图片</Select.Option>
                                <Select.Option value="video">视频</Select.Option>
                              </Select>
                            </Form.Item>
                            {uploadType[index] === 'image' && (
                              <Form.Item
                                label="播放时长"
                                name="time"
                                rules={[{ required: true, message: '请输入5-20秒' }]}
                                initialValue={uploadTime[index]}
                              >
                                <InputNumber
                                  placeholder="可输入5-20秒"
                                  min={5}
                                  max={20}
                                  style={{ width: '200px' }}
                                  addonAfter="秒"
                                  onChange={(e): void => setUploadTime(e, index)}
                                />
                              </Form.Item>
                            )}
                            {console.log(JSON.parse(JSON.stringify(fileList)))}
                            <Upload.Dragger
                              key={item.id}
                              disabled={
                                uploadType[index] === '' ||
                                (uploadType[index] === 'image' && uploadTime[index] === undefined)
                              }
                              {...uploadFormProps}
                              accept={uploadType[index] === 'image' ? '.jpg, .png' : '.mp4, .wav'}
                              beforeUpload={
                                (file, fileType): boolean => beforeUpload(
                                  file, fileType, item.resolution,
                                  )
                              }
                              fileList={fileList[index]}
                              onChange={(e): void => uploadChange(e, index, item.resolution)}
                              onPreview={(file): void => setImagePreview(file, this.formRef, index)}
                              onRemove={(): boolean => {
                                initBackgroundItem(index);
                                return true;
                              }}
                              previewFile={this.handlePreviewFile}
                              action={(file): string => {
                                return `${
                                  window.__FE_CONF_PROV__.apiPublicUrl
                                }/hfle/v1/${8}/files/secret-multipart?bucketName=${
                                  file.name.split('.').reverse()[0]
                                }&fileName=${file.name}`;
                              }}
                              className={style.uploadContainer}
                              style={{ display: fileList[index]?.length ? 'none' : 'block' }}
                              maxCount={1}
                            >
                              <img src={uploadIcon} alt="" />
                              <div className={style.uploadDescribe}>{item.resolution}px</div>
                            </Upload.Dragger>
                            <div
                              className={style.record}
                              onClick={(): Promise<void> => this.historyUpload('advert', index)}
                            >
                              <img src={uploadRecord} alt="" />
                              历史上传记录
                            </div>
                          </Form>
                        </Col>
                      );
                    })}
                </Row>
                <MaterialPreviewModal
                  url={imgUrl[modalIndex]}
                  ref={this.formRef}
                  type={uploadType[modalIndex]}
                />
                <div className={style.bottomButton}>
                  <Button
                    style={{ marginRight: '24px' }}
                    onClick={(): void => setAdvertisingModalVisible()}
                  >
                    取消
                  </Button>
                  {current > 0 && (
                    <Button
                      type="primary"
                      style={{ marginRight: '24px' }}
                      onClick={(): void => setCurrent('prev')}
                    >
                      上一步
                    </Button>
                  )}
                  <Button type="primary" onClick={(): void => saveRelease('material', '', this.advertisementListViewModel)}>
                    保存
                  </Button>
                  <Button type="primary" onClick={(): void => saveRelease('material', 'publish')} style={{ marginLeft: '24px' }}>
                    发布
                  </Button>
                </div>
              </div>
            )}
          </div>
        </div>
        <UploadHistoryRecordModal />
      </Modal>
    );
  }
}
