import React from 'react';
import { observer } from 'mobx-react';
import { Modal, Form, Button, Select, DatePicker, Cascader, Tooltip, FormInstance } from 'antd';
import DI from '../../../../inversify.config';
import { DATA_STATISTICS_IDENTIFIER } from '../../../../constants/identifiers';
import ExportModalViewModel from './viewModel';
import style from './style.less';
import timeTooips from '../../../../assets/images/time_tooips.svg';
import closeIcon from '../../../../assets/images/close_icon_normal.svg';

@observer
export default class ExportModal extends React.Component {
  private exportModalViewModel = DI.DIContainer.get<ExportModalViewModel>(
    DATA_STATISTICS_IDENTIFIER.ADVERT_STATISTICS_EXPORT_VIEW_MODEL,
  );

  private adRef = React.createRef<FormInstance>();
  
  public render(): JSX.Element {
    const {
      formOnFinish,
      exportModalVisible,
      setExportModalVisible,
      options,
      optionsAd,
      queryChange,
      query,
      setDevice,
      deviceValue,
      setAdvertisement,
      advertismentValue,
    } = this.exportModalViewModel;

    this.adRef.current?.setFieldsValue({ advert: advertismentValue, device: deviceValue });

    return (
      <Modal
        visible={exportModalVisible}
        width={550}
        closable={false}
        footer={null}
        wrapClassName={style.exportModalContainer}
        destroyOnClose
      >
        <div className={style.exportModalContent}>
          <div className={style.modalHeader}>
            详单导出
            <Button type="text" onClick={(): void => setExportModalVisible()}>
              <img src={closeIcon} alt="" />
            </Button>
          </div>
          <Form onFinish={formOnFinish} ref={this.adRef} labelCol={{ span: 5 }} wrapperCol={{ span: 21 }}>
            <div>
              <Form.Item label="查询方式" name="way" rules={[{ required: true }]}>
                <Select placeholder="请选择查询方式" onChange={queryChange}>
                  <Select.Option value="advertising">按广告查询</Select.Option>
                  <Select.Option value="equipment">按设备查询</Select.Option>
                </Select>
              </Form.Item>

              <Form.Item
                label={(
                  <div>
                    日期区间
                    <Tooltip placement="top" title="提示：选择的日期区间需小于3个月内">
                      <img className={style.timeImg} src={timeTooips} alt="" />
                    </Tooltip>
                  </div>
                )}
                name="time"
                rules={[{ required: true, message: '请选择日期区间' }]}
              >
                <DatePicker.RangePicker format="YYYY-MM-DD" style={{ width: '100%' }} />
              </Form.Item>
                  {console.log(advertismentValue, 'advertismentValue')}
              {
                query === 'advertising' && optionsAd.length>0?
                <Form.Item label="具体广告" name="advert" rules={[{ required: true }]} initialValue={advertismentValue ? advertismentValue: undefined}>
                  <Cascader
                    multiple
                    onChange={setAdvertisement}
                    options={optionsAd}
                    maxTagCount="responsive"
                    placeholder="请选择具体的广告"
                  />
                </Form.Item>
                :
                ''
              }
              {
                query === 'equipment' && options.length>2? 
                <Form.Item label="具体设备" name="device" rules={[{ required: true }]} initialValue={deviceValue? deviceValue: undefined}>
                <Cascader
                  multiple
                  onChange={setDevice}
                  options={options}
                  maxTagCount="responsive"
                  placeholder="请选择具体的设备"
                />
              </Form.Item>
              :
              ''
              }
   
            </div>
            <div className={style.bottomButton}>
              <Button onClick={(): void => setExportModalVisible()}>取消</Button>
              <Button type="primary" htmlType="submit">
                导出
              </Button>
            </div>
          </Form>
        </div>
      </Modal>
    );
  }
}
