import { inject, injectable } from 'inversify';
import { USER_IDENTIFIER } from '../../constants/identifiers';
import UserRepository from '../repositories/userRepository';
import { UserInfoEntity } from '../entities/userEntities';

@injectable()
export default class GoodsUseCase {
  @inject(USER_IDENTIFIER.USER_REPOSITORY)
  private userRepository!: UserRepository;

  public userInfo: UserInfoEntity;
  public constructor() {
    this.userInfo = {};
  }

  // 获取用户信息
  public async getUserInfo(): Promise<UserInfoEntity> {
    await this.userRepository
    .requestUserInfo()
    .then((res) => {
      this.userInfo = {...res};
    })
    .catch((err) => {
      console.log(err);
      this.userInfo = {};
    });
    return this.userRepository.requestUserInfo();
  }
}
