import axios from 'axios';
import { injectable } from 'inversify';
import { CommonPagesGeneric } from '../common/config/commonConfig';
import DefaultPageRepository from '../domain/repositories/defaultPageRepository';
import {
  PublishDefaultPageListItemConfig,
  DefaultPageListItemConfig,
  CreateDefaultEntity,
} from '../domain/entities/defaultPageEntities';

import {
  CreateMaterialEntity,
} from '../domain/entities/advertisementEntities';
@injectable()
export default class DefaultPageRepositoryImpl implements DefaultPageRepository {
  // 获取上屏发布-通知列表
  async requestPublishDefaultPageList(
    page: number,
    size: number,
    unitId?: number,
    deviceType?: string,
  ): Promise<CommonPagesGeneric<PublishDefaultPageListItemConfig>> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/default/advertise`,
      {
        params: {
          page,
          size,
          unitId,
          deviceType,
        },
      },
    );
    return data;
  }

  // 删除上屏发布-通知列表单条数据
  async requestDelPublishItem(id?: number): Promise<void> {
    await axios.delete(`${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/default/advertise/${id}`);
  }

  // 获取缺省管理-通知列表
  async requestDefaultPageList(
    page: number,
    size: number,
    deviceType?: string,
  ): Promise<CommonPagesGeneric<DefaultPageListItemConfig>> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/site/default/advertise`,
      {
        params: {
          page,
          size,
          deviceType,
        },
      },
    );
    return data;
  }

  // 删除缺省管理-通知列表单条数据
  async requestDelItem(id?: number): Promise<void> {
    await axios.delete(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/site/default//advertise/${id}`,
    );
  }
  // 上屏 - 新增缺省
  async requestCreateDefault(
    params: CreateDefaultEntity,
  ): Promise<void> {
    const { data } = await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/default/advertise`,
        params,
    );
    return data;
  }

  // 上屏 - 更新缺省
  async requestPutDefault(
    params: CreateDefaultEntity,
  ): Promise<void> {
    const { data } = await axios.put(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/default/advertise`,
        params,
    );
    return data;
  }

  // 上屏 -  缺省详情
  async requestDetailDefault(
    id: number,
  ): Promise<PublishDefaultPageListItemConfig> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/default/advertise/${id}`
    );
    return data;
  }

   // 新增缺省
   async requestPlatformCreateDefault(
    params: CreateDefaultEntity,
  ): Promise<void> {
    const { data } = await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/site/default/advertise`,
        params,
    );
    return data;
  }

  //更新缺省
  async requestPlatformPutDefault(
    params: CreateDefaultEntity,
  ): Promise<void> {
    const { data } = await axios.put(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/site/default/advertise`,
        params,
    );
    return data;
  }

  // 缺省详情
  async requestPlatformDetailDefault(
    id: number,
  ): Promise<PublishDefaultPageListItemConfig> {
    const { data } = await axios.get(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/site/default/advertise/${id}`
    );
    return data;
  }

  // 创建素材
  async requestCreateMaterial(params?: CreateMaterialEntity): Promise<number> {
    const { data } = await axios.post(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/site/material`,
      params,
    );
    return data;
  }

  // 删除素材
  async requestDelMaterial(materialId?: number): Promise<void> {
    const { data } = await axios.delete(
      `${window.__FE_CONF_PROV__.apiPublicUrl}/tmis/v1/site/material/${materialId}`,
    );
    return data;
  }
}
